import React from "react";
import { Radio, Form } from "antd";
function AuthInputRadio() {
  return (
    <>
      <Form.Item name="type" required>
        <Radio.Group>
          <Radio.Button value="clinic">
            <img src="imgs/icons/auth/doctorv.svg" alt="clinic" />
          </Radio.Button>
          <Radio.Button value="laboratory_center">
            <img src="imgs/icons/auth/lab.svg" alt="lab" />
          </Radio.Button>
          <Radio.Button value="radiology_center">
            <img src="imgs/icons/auth/rad.svg" alt="radiology" />
          </Radio.Button>
          <Radio.Button value="medical_center">
            <img src="imgs/icons/auth/hospital.svg" alt="medical" />
          </Radio.Button>
          <Radio.Button value="pharmacy">
            <img src="imgs/icons/auth/pharmacy.svg" alt="pharmacy" />
          </Radio.Button>
          <Radio.Button value="pharma_company">
            <img src="imgs/icons/auth/company.svg" alt="pharma" />
          </Radio.Button>
        </Radio.Group>
      </Form.Item>
    </>
  );
}
export default AuthInputRadio;
