import React, { useContext } from "react";
import { Button } from "antd";

import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";
// import { loginService } from "../../../../Services/AuthServices/LoginService";

import "./AuthSocialMedia.less";
import { useHistory } from "react-router-dom";
import { LoginFormContext } from "../AuthLogin/Login";
import { SignupFormContext } from "../AuthSignup/SignupOptions/SignupOptions";

function AuthFacebookAccount(props) {
  const history = useHistory();
  const signupForm = useContext(SignupFormContext);
  const loginForm = useContext(LoginFormContext);

  const successResponse = (response) => {
    // Sign Up Option
    if (Object.keys(signupForm).length !== 0) {
      signupForm.setFieldsValue({ username: response.name });
      signupForm.setFieldsValue({ email: response.email });
      const token = {
        type: "FACEBOOK",
        accessToken: response.accessToken,
        accountId: response.userID,
      };
      signupForm.setFieldsValue({ socialAccount: token });
      console.log(token);
    }
    // Login Option
    if (Object.keys(loginForm).length !== 0) {
      const loginData = {
        type: "FACEBOOK",
        username: response.name,
        password: response.accessToken,
      };
      // loginService(loginData, history, loginForm);
      console.log(loginData);
    }
  };

  const failureResponse = () => {
    console.log("mlkknknk");
  };

  return (
    <div className="auth-facebook">
      {/* <Button type="link" icon={<img src="/imgs/icons/auth/socialMedia/facebook.svg" alt='Facebook' />} size='small'></Button> */}
      <FacebookLogin
        appId="884990765330909"
        render={(renderProps) => (
          <Button
            onClick={renderProps.onClick}
            type="link"
            icon={
              <img
                src="/imgs/icons/auth/socialMedia/facebook.png"
                alt="Facebook"
              />
            }
            size="small"
          ></Button>
        )}
        buttonText="Login"
        // autoLoad={true}
        fields="name,email"
        onClick={failureResponse}
        callback={successResponse}
        cookiePolicy={"single_host_origin"}
      />
    </div>
  );
}
export default AuthFacebookAccount;
