import React from "react";
import { Modal } from "antd";
import ReactCodeInput from "react-verification-code-input";
// import "react-phone-input-2/lib/style.css";

function InvitationCodeModal({
  invitationCodeVisible,
  handleOk,
  cancelInvitationModal,
}) {
  return (
    <div>
      <Modal
        visible={invitationCodeVisible}
        // centered
        onOk={handleOk}
        className="invitation-modal"
      >
        <h3>Enter Invitation Code</h3>
        <p>Please enter the invitation code sent to you by a UMEDMI user</p>
        <ReactCodeInput fields={4} autoFocus={true} autoFocus type="text" />
        <span className="modal-actions">Submit</span>
        <span className="modal-actions" onClick={cancelInvitationModal}>
          Cancel
        </span>
      </Modal>
    </div>
  );
}

export default InvitationCodeModal;
