export function loginErrors(errorMessage, form) {
  if (errorMessage === "Invalid username") {
    form.setFields([
      {
        name: "username",
        errors: ["Invalid data"],
      },
    ]);
  } else if (errorMessage === "Invalid Credentials") {
    form.setFields([
      {
        name: "password",
        errors: ["Invalid password"],
      },
    ]);
  }
}
