import React, { useContext, createContext } from 'react';
import { Tabs } from 'antd';
import AuthPersonalInfo from './AuthPersonalInfo/AuthPersonalInfo';
import AuthMedicalFacility from './AuthMedicalFacility/AuthMedicalFacility';
import { TabContext } from '../AuthProfileComplete';
import {
  addFacilities,
  facilities,
  facilitiesNames,
  addFacilityName,
  branches,
  addBranches,
  branchesNames,
  addBranchName,
} from './FacilityContext';
const { TabPane } = Tabs;

function AuthPersonalFacilityInformation() {
  const activeTab = useContext(TabContext);
  return (
    <div>
      <Tabs activeKey={activeTab}>
        <TabPane tab='Personal' key='1'>
          <AuthPersonalInfo />
        </TabPane>

        <TabPane tab='Medical Facilities' key='2'>
          <addBranches.Provider value={addBranchName}>
            <branches.Provider value={branchesNames}>
              <facilities.Provider value={facilitiesNames}>
                <addFacilities.Provider value={addFacilityName}>
                  <AuthMedicalFacility />
                </addFacilities.Provider>
              </facilities.Provider>
            </branches.Provider>
          </addBranches.Provider>

          <facilities.Provider value={facilitiesNames}>
            <addFacilities.Provider value={addFacilityName}>
              <AuthMedicalFacility />
            </addFacilities.Provider>
          </facilities.Provider>
        </TabPane>
      </Tabs>
    </div>
  );
}
export default AuthPersonalFacilityInformation;
