import * as React from 'react';
import Countdown, { zeroPad } from 'react-countdown';

import './CountdownTime.less';

type Iprops = {
  time: number;
  title: string;
};

function CountdownTime({ time, title }: Iprops) {
  const Completionist = () => <span>You are good to go!</span>;

  const renderer = ({ hours, minutes, seconds, completed }) => {
    if (completed) {
      return <Completionist />;
    } else {
      return (
        <div className="umi-countdown">
          <div className="umi-time">
            <span className="umi-time-pad">{zeroPad(hours)}</span>
            <span className="umi-label">Hrs</span>
          </div>
          <div className="umi-time">
            <span className="umi-time-pad">{zeroPad(minutes)}</span>
            <span className="umi-label">Min</span>
          </div>
          <div className="umi-time">
            <span className="umi-time-pad">{zeroPad(seconds)}</span>
            <span className="umi-label">Sec</span>
          </div>
        </div>
      );
    }
  };

  return (
    <div className="umi-countdown-wrapper">
      <div className="umi-img-wrapper">
        <img src="icons/alarm-clock.svg" alt="Alarm Clock" />
      </div>
      <div className="umi-time-section">
        <div className="umi-title">{title}</div>
        <Countdown date={Date.now() + time} renderer={renderer} zeroPadTime={2} />
      </div>
    </div>
  );
}

export default CountdownTime;
