import React from "react";
import { Form, Select } from "antd";
const { Option } = Select;

function LookupCustomSelect({
  name,
  label,
  selectOption,
  handleSelectData,
  setSelectOption,
  allData,
}) {
  return (
    <div>
      <Form.Item name={name} label={label}>
        <Select
          showSearch={true}
          onSearch={(value) => setSelectOption(value)}
          onChange={(value) => handleSelectData(value, { name })}
        >
          {allData.map((data, index) => (
            <Option value={data.name.en} key={index}>
              {data.name.en}
            </Option>
          ))}
          <Option value={selectOption}>{selectOption}</Option>
        </Select>
      </Form.Item>
    </div>
  );
}

export default LookupCustomSelect;
