import React from "react";
import AuthGoogleAccount from "./AuthGoogleAccount";
import AuthFacebookAccount from "./AuthFacebookAccount";
import AuthLinkedInAccount from "./AuthLinkedInAccount";
import AuthUmedMiAccount from "./AuthUmedMiAccount"
import "./AuthSocialMedia.less";
import { Row, Col } from "antd";

function SocialMedia({ title }) {

    return (
        <fieldset className='auth-social-media'>
            <legend className='title'> {title} </legend>
            <Row className='Social'>
                <Col span={6}> <AuthUmedMiAccount /> </Col>
                <Col span={6}> <AuthGoogleAccount /> </Col>
                <Col span={6}> <AuthFacebookAccount /> </Col>
                <Col span={6}> <AuthLinkedInAccount /> </Col>
            </Row>
        </fieldset>
    );
}
export default SocialMedia;
