import React from "react";
import { Carousel } from "antd";
import { PushpinOutlined, UpOutlined } from "@ant-design/icons";
import "./AuthAds.less";
type Iprops = {
  onPopoverClick?: any;
  onFixedPinClick?: any;
};
function AuthAds({ onPopoverClick, onFixedPinClick }: Iprops) {
  const onCancelAd = () => {
    console.log(document.querySelector(".ad-description"));
  };
  return (
    <div className="auth-ads">
      <div className="auth-ads-icons">
        <PushpinOutlined onClick={onFixedPinClick} />
        <UpOutlined onClick={onPopoverClick} />
      </div>
      <Carousel autoplay={true} dots={false}>
        <div className="ad-description">
          <div className="auth-ads-content">
            <p>Ad should be visible here</p>
          </div>
          <div className="auth-ads-buttons">
            <span onClick={onCancelAd}>I don't want to see this</span>
            <button>Learn More</button>
          </div>
        </div>
        <div className="ad-description">
          <div className="auth-ads-content">
            <p>Ad should be visible here</p>
          </div>
          <div className="auth-ads-buttons">
            <span onClick={onCancelAd}>I don't want to see this</span>
            <button>Learn More</button>
          </div>
        </div>
      </Carousel>
    </div>
  );
}
export default AuthAds;
