import React from "react";
import { Button } from "antd";
import { ArrowLeftOutlined } from "@ant-design/icons";

function AuthProfileCompleteActions({ current, steps, prev, next }) {
  return (
    <div className="steps-action">
      {current > 0 && (
        <div>
          <Button className="back-btn" onClick={prev}>
            <ArrowLeftOutlined />
            Back
          </Button>
        </div>
      )}
      {current == 0 && (
        <div>
          <Button disabled className="back-btn">
            <ArrowLeftOutlined />
            Back
          </Button>
        </div>
      )}
      {current < steps.length - 1 && (
        <div>
          <Button className="proceed-btn" onClick={next}>
            Proceed
          </Button>
        </div>
      )}
      {current === steps.length - 1 && (
        <div>
          <Button className="proceed-btn" htmlType="submit">
            Done
          </Button>
        </div>
      )}
    </div>
  );
}

export default AuthProfileCompleteActions;
