import React from "react";
import { Row, Col } from "antd";
import "./MainInvitation.less";
import MainInvitationAside from "./MainInvitationAside/MainInvitationAside";
import MainInvitationContent from "./MainInvitationContent/MainInvitationContent";

function MainInvitation() {
  return (
    <div className="main-invitation">
      <Row justify="space-between">
        <Col span="8">
          <aside>
            <MainInvitationAside />
          </aside>
        </Col>
        <Col span="16">
          <main>
            <MainInvitationContent />
          </main>
        </Col>
      </Row>
    </div>
  );
}

export default MainInvitation;
