import React from "react";
import { Row, Col, Form, Input } from "antd";
import "./AuthPersonalInfoId.less";
import AuthInput from "../../../Inputs/AuthInput/AuthInput";
import AuthInputSelect from "../../../Inputs/AuthInputSelect";

function AuthPersonalInfoId() {
  const options = [
    { value: "national_id", data: "National ID" },
    { value: "passport", data: "passport" },
  ];
  return (
    <div className="auth-personal-info-id">
      <label>National ID / Passport</label>
      <div className="auth-personal-info-id-data">
        <Row justify="space-between">
          <Col span={8}>
            <AuthInputSelect
              inputSelect
              inputSelectName={["personalId", "idType"]}
              inputSelectPlaceholder="Type"
              options={options}
            />
          </Col>
          <Col span={15}>
            <Form.Item name={["personalId", "idData"]}>
              <Input placeholder="National ID/Passport" />
            </Form.Item>
            {/* <AuthInput
              inputText
              inputTextName={["personalInfoId", "idData"]}
              inputTextPlaceholder="National ID/Passport"
            /> */}
          </Col>
        </Row>
      </div>
    </div>
  );
}

export default AuthPersonalInfoId;
