export function signupErrors(errorMessage, form) {
  if (errorMessage.includes("phoneNumber must be a valid phone number")) {
    form.setFields([
      {
        name: "phoneNumber",
        errors: ["phoneNumber must be a valid phone number"],
      },
    ]);
  }
  if (errorMessage.includes("phoneNumber must be unique")) {
    {
      form.setFields([
        {
          name: "phoneNumber",
          errors: ["phoneNumber must be unique"],
        },
      ]);
    }
  }
  if (errorMessage.includes("username must be unique")) {
    {
      form.setFields([
        {
          name: "username",
          errors: ["username must be unique"],
        },
      ]);
    }
  }
  if (errorMessage.includes("email must be unique")) {
    {
      form.setFields([
        {
          name: "email",
          errors: ["Email must be unique"],
        },
      ]);
    }
  }
}
