import React from 'react';

import './DoctorDataReport.less';

function DoctorDataReport() {
  return (
    <div className="umi-doctor-info">
      <div className="umi-doctor-name">
        <div className="umi-name">
          <h2>Dr/ Ahmed Hassan</h2>
        </div>
        <div className="umi-logo">
          <img src="imgs/logo.png" alt="Logo" />
        </div>
      </div>
      <div>Professor of Heart and Blood vessels</div>
      <div>CPHO-TOM-PhD-FC-Harverd University</div>
    </div>
  );
}

export default DoctorDataReport;
