import React, { useState } from 'react';
import { Modal } from 'antd';

import { ShareAltOutlined, CopyOutlined } from '@ant-design/icons';
import { CopyToClipboard } from 'react-copy-to-clipboard';

import {
  FacebookShareButton,
  FacebookIcon,
  EmailShareButton,
  WhatsappShareButton,
  LinkedinShareButton,
  TwitterShareButton,
  EmailIcon,
  WhatsappIcon,
  LinkedinIcon,
  TwitterIcon,
} from 'react-share';
import './InvitationShare.less';

function InvitationShare() {
  const [visible, setVisible] = useState(false);

  const [copied, setCopied] = useState(false);

  const showShare = () => {
    setVisible(true);
  };
  const handleOk = () => {
    setVisible(false);
  };
  const handleCancel = () => {
    setVisible(false);
  };
  return (
    <div className='invitation-share'>
      <ShareAltOutlined onClick={showShare} />

      <CopyToClipboard text='test' onCopy={() => setCopied(true)}>
        <CopyOutlined />
      </CopyToClipboard>

      {copied ? <span style={{ color: 'red' }}>Copied.</span> : null}

      <Modal title='Share' visible={visible} onOk={handleOk} onCancel={handleCancel}>
        <EmailShareButton subject='' body='' url='https://umedmi.com/'>
          <EmailIcon size={32} round={true} />
        </EmailShareButton>
        <FacebookShareButton quote='' hashtag='' url='https://umedmi.com/'>
          <FacebookIcon size={32} round={true} />
        </FacebookShareButton>
        <WhatsappShareButton url='https://umedmi.com/' title=''>
          <WhatsappIcon size={32} round={true} />
        </WhatsappShareButton>
        <LinkedinShareButton url='https://umedmi.com/' title='umedmi'>
          <LinkedinIcon size={32} round={true} />
        </LinkedinShareButton>
        <TwitterShareButton url='https://umedmi.com/' title='umedmi'>
          <TwitterIcon size={32} round={true} />
        </TwitterShareButton>
      </Modal>
    </div>
  );
}

export default InvitationShare;
