import React from 'react';
import MoreButton from '../PanelInputs/MoreButton';
import TemplateList from '../TemplateList/TemplateList';

import './PanelControl.less';

type IProps = {
  moreButton?: boolean;
  panelKey?: string;
  children: any;
};

function PanelControl({ moreButton, panelKey, children }: IProps) {
  return (
    <div className="panel-control-wrapper">
      <TemplateList />
      {children}
      {moreButton && <MoreButton panelKey={panelKey} />}
    </div>
  );
}

export default PanelControl;
