import React, { useContext } from "react";
import { Form } from "antd";
import ReactFlagsSelect from "react-flags-select";
import { FormContext } from "../../AuthProfileComplete";
import "react-flags-select/css/react-flags-select.css";
import "./AuthInputCountry.less";

type Iprops = {
  inputCountryName?: any;
  inputCountryLabel?: string;
  fieldParentArr?: any;
};
function AuthInputCountry({
  inputCountryLabel,
  inputCountryName,
  fieldParentArr,
}: Iprops) {
  const form = useContext(FormContext);
  // const fieldData: any = [];

  return (
    <div className="auth-input-country">
      <Form.Item name={inputCountryName} label={inputCountryLabel}>
        <ReactFlagsSelect
          defaultCountry="EG"
          onSelect={(country) => {
            // fieldData.push({ [inputCountryName]: country });
            // form.setFieldsValue({ [fieldParentArr]: fieldData });
          }}
        />
      </Form.Item>
    </div>
  );
}
export default AuthInputCountry;

