import React, { useEffect, useState } from "react";
import { Row, Col, Form, Checkbox } from "antd";
import axios from "axios";
import AuthInput from "../Inputs/AuthInput/AuthInput";
import AuthInputSelect from "../Inputs/AuthInputSelect";
import { ContactInfoItemData } from "./FirstStepData/IntroData";
import AuthInputCountry from "../Inputs/AuthInputCountry/AuthInputCountry";
import AuthAddMoreDynamicForm from "../Inputs/AuthAddMoreDynamicForm/AuthAddMoreDynamicForm";
import LookupCustomSelect from "./FirstStepUtils/LookupCustomSelect";

function AuthIntro() {
  const contactInfoItemData = ContactInfoItemData;
  const [primaryPractice, setPrimaryPractice] = useState([
    { name: { en: "" } },
  ]);
  const [recentEducation, setRecentEducation] = useState([
    { name: { en: "" } },
  ]);
  const [educationType, setEducationType] = useState([{ name: { en: "" } }]);
  const [industry, setIndustry] = useState([{ name: { en: "" } }]);
  const primaryPracticeData = [] as any;
  const recentEducationData = [] as any;
  const educationTypeData = [] as any;
  const industryData = [] as any;
  const [selectOption, setSelectOption] = useState("");
  useEffect(() => {
    axios
      .all([
        axios.get("/u-consultation/lookup/primaryPractice"),
        axios.get("/u-consultation/lookup/recentEducation"),
        axios.get("/u-consultation/lookup/educationType"),
        axios.get("/u-consultation/lookup/industry"),
      ])
      .then((response) => {
        setPrimaryPractice(response[0].data.data);
        setRecentEducation(response[1].data.data);
        setEducationType(response[2].data.data);
        setIndustry(response[3].data.data);
      });
  }, []);

  const handleSelectData = (selectedValue, type) => {
    if (type.name === "primaryPractice") {
      primaryPractice.map((data) => {
        primaryPracticeData.push(data.name.en);
      });
      if (!primaryPracticeData.includes(selectedValue)) {
        testPostData(selectedValue, "primaryPractice");
      }
    }
    if (type.name === "recentEducation") {
      recentEducation.map((data) => {
        recentEducationData.push(data.name.en);
      });
      if (!recentEducationData.includes(selectedValue)) {
        testPostData(selectedValue, "recentEducation");
      }
    }
    if (type.name === "educationType") {
      educationType.map((data) => {
        educationTypeData.push(data.name.en);
      });
      if (!educationTypeData.includes(selectedValue)) {
        testPostData(selectedValue, "educationType");
      }
    }
    if (type.name === "industry") {
      industry.map((data) => {
        industryData.push(data.name.en);
      });
      if (!industryData.includes(selectedValue)) {
        testPostData(selectedValue, "industry");
      }
    }
  };
  const testPostData = (value, type) => {
    const data = {
      type: type,
      name: {
        en: value,
      },
    };
    axios.post("/u-consultation/lookup", data);
  };

  return (
    <div className="auth-intro">
      <div className="bg-add-new">
        <Row justify="space-between">
          <Col span={11}>
            <AuthInput
              inputText
              inputTextLabel="Headline"
              inputTextName="headLine"
              inputTextPlaceholder="Ex. Assistant Professor in Cairo University"
            />
          </Col>

          <Col span={11}>
            <LookupCustomSelect
              name="primaryPractice"
              label="Primary Practice"
              selectOption={selectOption}
              handleSelectData={handleSelectData}
              setSelectOption={setSelectOption}
              allData={primaryPractice}
            />
          </Col>
          <Col span={11}>
            <LookupCustomSelect
              name="recentEducation"
              label="recent Education"
              selectOption={selectOption}
              handleSelectData={handleSelectData}
              setSelectOption={setSelectOption}
              allData={recentEducation}
            />
          </Col>
          <Col span={11}>
            <LookupCustomSelect
              name="educationType"
              label="Education Type"
              selectOption={selectOption}
              handleSelectData={handleSelectData}
              setSelectOption={setSelectOption}
              allData={educationType}
            />
          </Col>
          <Form.Item name="showHeadlines" valuePropName="checked">
            <Checkbox checked={false}>Show this info in my intro</Checkbox>
          </Form.Item>
        </Row>
      </div>
      <div className="bg-add-new">
        <Row justify="space-between">
          <Col span={24}>
            <LookupCustomSelect
              name="industry"
              label="Industry"
              selectOption={selectOption}
              handleSelectData={handleSelectData}
              setSelectOption={setSelectOption}
              allData={industry}
            />
          </Col>
          <Col span={7}>
            <AuthInputCountry
              inputCountryLabel="Country"
              inputCountryName={["residence", "countryId"]}
              // inputCountryName="country"
            />
          </Col>
          <Col span={16}>
            <AuthInputSelect
              inputSelect
              inputSelectPlaceholder="City"
              inputSelectLabel="City"
            />
          </Col>
          <Col span={24}>
            <AuthAddMoreDynamicForm
              formListName="contactInfo"
              formItemData={contactInfoItemData}
              backgroundStyle="bg-add-new"
            />
          </Col>
        </Row>
      </div>
    </div>
  );
}

export default AuthIntro;
