import React from "react";
import { Tabs, Divider, Row, Col } from "antd";
import { ArrowLeftOutlined } from "@ant-design/icons";
import SubscriptionCard from "./SubscriptionCard/SubscriptionCard";
import { SubscriptionData } from "./SubscriptionCard/SubscriptionCardData";
import Invitation from "./Invitation/Invitation";
import "./SubscriptionPlan.less";
import { Link } from "react-router-dom";

const { TabPane } = Tabs;
const subscriptionData = SubscriptionData;
function SubscriptionPlan() {
  return (
    <div className="auth-success-subscription">
      <Tabs defaultActiveKey="1">
        <TabPane tab="Individual" key="1"></TabPane>
        <TabPane tab="Corporate" disabled key="2"></TabPane>
      </Tabs>

      <Invitation />
      <Divider>Or choose from one of our plans</Divider>
      <Row justify="space-between">
        <Col span={5}>
          <div className="subscription-heads">
            <SubscriptionCard
              price="PRICE IN LE"
              messages="MESAGES"
              sharing="SHARING"
              storage="STORAGE"
              sharingPatients="SHARING PATIENTS"
              viewSharedPatients="VIEW SHARED PATIENTS"
              pollFeature="POLL FEATURE"
              messageDeletion="MESSAGE RETENTION & DELETION"
              umedmiBot="UMEDMI-BOT"
              videoCall="CALL & VIDEO FEATURE"
              scheduleEvents="SCHEDULING EVENTS"
              adsBlocker="ADS BLOCKER"
            />
          </div>
        </Col>
        <Col span={18}>
          <div className="subscription-cards">
            {subscriptionData.map((subscrep) => (
              <SubscriptionCard
                key={subscrep.key}
                subscriptionType={subscrep.subscriptionType}
                price={subscrep.price}
                messages={subscrep.messages}
                sharing={subscrep.sharing}
                storage={subscrep.storage}
                sharingPatients={subscrep.sharingPatients}
                viewSharedPatients={subscrep.viewSharedPatients}
                pollFeature={subscrep.pollFeature}
                messageDeletion={subscrep.messageDeletion}
                umedmiBot={subscrep.umedmiBot}
                videoCall={subscrep.videoCall}
                scheduleEvents={subscrep.scheduleEvents}
                adsBlocker={subscrep.adsBlocker}
                typeColor={subscrep.typeColor}
              />
            ))}
          </div>
        </Col>
      </Row>
      <div className="subscription-back">
        <Link to="/completeSignup">
          <span>
            <ArrowLeftOutlined /> Back
          </span>
        </Link>
      </div>
    </div>
  );
}

export default SubscriptionPlan;
