import axios from "axios";

export function getMainSpecialty(businessType: string) {
  return axios.get(`${process.env.REACT_APP_UMEDMI_SERVER}/u-consultation/speciality/${businessType}`);
}

export function getSubSpecialty(specialityId: number) {
  return axios.get(`${process.env.REACT_APP_UMEDMI_SERVER}/u-consultation/speciality/${specialityId}/sub-speciality`);
}
