import React from "react";
import AuthAddMoreDynamicForm from "../Inputs/AuthAddMoreDynamicForm/AuthAddMoreDynamicForm";
import { LicensureMedicalFacilityItemData } from "./FourthStepData/LicensureMedicalFacilityData";

function AuthLicensureMedicalFacility() {
  const licensureMedicalFacilityItemData = LicensureMedicalFacilityItemData();
  return (
    <div>
      <div className="personal-licensure-section" style={{ marginTop: "20px" }}>
        <AuthAddMoreDynamicForm
          formListName="licensureMedicalFacility"
          backgroundStyle="bg-add-new"
          formItemData={licensureMedicalFacilityItemData}
        />
      </div>
    </div>
  );
}

export default AuthLicensureMedicalFacility;
