import React, { useContext } from "react";
import { Button } from "antd";
import { useHistory } from "react-router-dom";
import { GoogleLogin } from "react-google-login";
// import { loginService } from "../../../../Services/AuthServices/LoginService";
import { SignupFormContext } from "../AuthSignup/SignupOptions/SignupOptions";
import { LoginFormContext } from "../AuthLogin/Login";
import "./AuthSocialMedia.less";
function AuthGoogleAccount(props) {
  const history = useHistory();
  const signupForm = useContext(SignupFormContext);
  const loginForm = useContext(LoginFormContext);
  const successResponse = (response) => {
    // Sign Up Option
    if (Object.keys(signupForm).length !== 0) {
      signupForm.setFieldsValue({ username: response.profileObj.name });
      signupForm.setFieldsValue({ email: response.profileObj.email });
      const token = {
        type: "GOOGLE",
        accessToken: response.accessToken,
        accountId: response.googleId,
      };
      signupForm.setFieldsValue({ socialAccount: token });
      console.log(token);
    }
    // Login Option
    if (Object.keys(loginForm).length !== 0) {
      const loginData = {
        type: "GOOGLE",
        username: response.profileObj.name,
        password: response.accessToken,
      };
      // loginService(loginData, history, loginForm);
      console.log(loginData);
    }
  };

  const failureResponse = (error) => {
    console.log(error);
  };
  return (
    <div className="auth-google">
      {/* <Button type="link" icon={<img src="/imgs/icons/auth/socialMedia/google.svg" alt='Google' />} size='small'></Button> */}
      <GoogleLogin
        // clientId="530577989890-3to4tcndin9v45gt2bvu34dur6bmufmu.apps.googleusercontent.com"
        clientId="899577426944-8loplggctpj443m6vm9pmqnu9qfk27k6.apps.googleusercontent.com"
        render={(renderProps) => (
          <Button
            type="link"
            icon={
              <img src="/imgs/icons/auth/socialMedia/google.png" alt="Google" />
            }
            size="small"
            onClick={renderProps.onClick}
            disabled={renderProps.disabled}
          ></Button>
        )}
        buttonText="Login"
        onSuccess={successResponse}
        onFailure={failureResponse}
        cookiePolicy={"single_host_origin"}
      />
    </div>
  );
}
export default AuthGoogleAccount;
