import React, { useState } from "react";
import { Button } from "antd";

import "./InvitationModals.less";
import InvitationFormModals from "./InvitationFormModal";
import InvitationCodeModal from "./InvitationCodeModal";
function InvitationModals() {
  const [invitationCodeVisible, setInvitationCode] = useState(false);
  const [invitationFormVisible, setInvitationForm] = useState(false);

  const showInvitationCode = () => {
    setInvitationCode(true);
  };
  const showInvitationForm = () => {
    setInvitationForm(true);
  };
  const handleOk = () => {
    setInvitationCode(false);
    setInvitationForm(false);
  };
  const cancelInvitationModal = () => {
    setInvitationCode(false);
    setInvitationForm(false);
  };
  return (
    <div className="invitation-modals">
      <div className="invitation-modals-buttons">
        <Button className="invite" onClick={showInvitationForm}>
          Invite Now
        </Button>

        <Button className="add-code" onClick={showInvitationCode}>
          Add Invitation Code
        </Button>
        {/* //modals */}

        <InvitationFormModals
          invitationFormVisible={invitationFormVisible}
          handleOk={handleOk}
          cancelInvitationModal={cancelInvitationModal}
        />
        <InvitationCodeModal
          invitationCodeVisible={invitationCodeVisible}
          handleOk={handleOk}
          cancelInvitationModal={cancelInvitationModal}
        />
      </div>
    </div>
  );
}
export default InvitationModals;
