import React, { useContext, useEffect } from 'react';
import SpeechRecognition from 'react-speech-recognition';
import { FormContext } from '../../PatientDetails/PatientDetalis';

const options = {
  autoStart: false,
};

const Dictaphone = ({
  transcript,
  resetTranscript,
  startListening,
  stopListening,
  browserSupportsSpeechRecognition,
  inputName,
}) => {
  const form = useContext(FormContext);

  useEffect(() => {
    if (transcript) {
      form.setFieldsValue({ [inputName]: transcript });
    }
  }, [transcript]);

  if (!browserSupportsSpeechRecognition) {
    return null;
  }

  const startTranscript = () => {
    if (!options.autoStart) {
      resetTranscript();
      startListening();
      options.autoStart = true;
    } else {
      stopListening();
      options.autoStart = false;
    }
  };

  return (
    <div className='speech-recognation' onClick={startTranscript}>
      <img src='icons/microphone.svg' alt='Mic Image' />
    </div>
  );
};

export default SpeechRecognition(options)(Dictaphone);
