import React from "react";
import ForgetVerifyProcess from "../ForgetVerifyProcess";
function VerifyPasswordEmail() {
  return (
    <div className="verify">
      <ForgetVerifyProcess
        head="Forgot Password"
        descreption="Please enter the pin sent to your email address emailaddress@domain.com"
        button="Submit"
        contentCode
        footer="Reset password with your verified Phone Number"
        footerlink="/verify-password-phone"
        extraData="Didn't Receive Email? "
        extraDataLink="Resend in 1:30 minutes"
      />
    </div>
  );
}
export default VerifyPasswordEmail;
