import * as React from 'react';
import { Collapse } from 'antd';
import UmiInputs from '../../Common/PanelInputs/UmiInputs';
import UmiTextarea from '../../Common/PanelInputs/UmiTextarea';
import PanelInputs from '../../Common/PanelInputs/PanelInputs';
import ExportExcel from '../../Common/ExportExcel/ExportExcel';
import PanelControl from '../../Common/PanelControl/PanelControl';
import SearchInput from '../../Common/PanelInputs/SearchInput/SearchInput';

import './DiagnosisPlan.less';

const { Panel } = Collapse;

function DiagnosisPlan() {
  const ExportExcelRender = () => <ExportExcel activeTab="diagnosisPlan" tabName="Diagnosis Plan" />;

  return (
    <Collapse defaultActiveKey={['diagnosis']} bordered={false}>
      <Panel header="Diagnosis" key="diagnosis">
        <PanelControl moreButton panelKey="diagnosis">
          <ExportExcelRender />
        </PanelControl>
        <PanelInputs>
          <div className="inputs-container">
            <SearchInput name="diagnosis-subject" placeholder="Subject" />
            <UmiInputs type="number" name="diagnosis-id" placeholder="#" />
          </div>
          <UmiTextarea name="diagnosis-complain" placeholder="Write The Main Complain ..." />
        </PanelInputs>
      </Panel>
      <Panel header="Plan" key="plan">
        <PanelControl moreButton panelKey="plan">
          <ExportExcelRender />
        </PanelControl>
        <PanelInputs
          formTitle="formTitle"
          formTitleContent="Choose your applied plan"
          showPlanCheckbox="showPlanCheckbox"
          oneTextareaHolder="Notes"
        >
          <UmiTextarea name="plan-notes" placeholder="Notes" />
        </PanelInputs>
      </Panel>
    </Collapse>
  );
}

export default DiagnosisPlan;
