import * as React from "react";

import "./ClinicTitle.less";

type Iprops = {
  title: String,
  count?: Number
}

function ClinicTitle({ title, count }: Iprops) {
  return (
    <div className="umi-clinic-title-wrapper">
      <div className="umi-clinic-title">{title}</div>
      {count &&
        <div className="umi-patients-count">{count}</div>
      }
    </div>
  );
}

export default ClinicTitle;
