import React from 'react';
import { Table, Tabs } from 'antd';
import { FlagOutlined } from '@ant-design/icons';

import './PatientInfoOthers.less';

type IProps = {
  panelKey?: string;
};

const { TabPane } = Tabs;

const columns = [
  {
    title: 'Test',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Date',
    dataIndex: 'age',
    key: 'age',
  },
  {
    title: 'Facility/Provider',
    dataIndex: 'address',
    key: 'address',
  },
  {
    title: 'Abnormal Result?',
    dataIndex: 'outcome',
    key: 'outcome',
  },
  {
    title: 'Red flag',
    dataIndex: 'flag',
    key: 'flag',
    render: (flag: boolean) => flag && <FlagOutlined />,
  },
];

const data = [
  {
    key: '1',
    name: 'Cholesterol',
    age: '12-5-2005',
    address: 'Write data here',
    outcome: 'Yes',
    flag: true,
  },
  {
    key: '2',
    name: 'colonocopy',
    age: '12-5-2005',
    address: 'Write data here',
    outcome: 'Yes',
    flag: true,
  },
  {
    key: '3',
    name: 'Mammogram',
    age: '12-5-2005',
    address: 'Write data here',
    outcome: 'Yes',
    flag: false,
  },
  {
    key: '4',
    name: 'Pap Smear',
    age: '12-5-2005',
    address: 'Write data here',
    outcome: 'Yes',
    flag: false,
  },
];

function PatientInfoOthers({ panelKey }: IProps) {
  return (
    <div className="umi-patient-others-wrapper">
      <Tabs defaultActiveKey="1">
        <TabPane tab="Screening Tests" key="1" />
        <TabPane tab="Personal" key="2" />
        <TabPane tab="Family" key="3" />
        <TabPane tab="Social History" key="4" />
        <TabPane tab="Women History" key="5" />
        <TabPane tab="Other Health Issues" key="6" />
      </Tabs>
      <Table columns={columns} dataSource={data} pagination={false} />
    </div>
  );
}

export default PatientInfoOthers;
