import React, { createContext } from 'react';
import { FlagOutlined } from '@ant-design/icons';
import { Input, Row, Col, Checkbox, Select } from 'antd';
import UmiInputs from './UmiInputs';
import UmiTextarea from './UmiTextarea';

import './PanelInputs.less';

export const panelContext = createContext({});

type Iprops = {
  inputsBeside?: boolean;
  inputLine?: any;
  oneTextareaHolder?: string;
  status?: any;
  inputsLine?: any;
  formTitle?: any;
  showExaminationCheckbox?: any;
  formTitleContent?: string;
  showPlanCheckbox?: any;
  inputsLineContentOne?: string;
  inputsLineContentTwo?: string;
  otherTextarea?: any;
  saveChangeBtn?: any;
  showExaminationsySystemCheckboc?: any;
  referral?: any;
  Onetextarea?: boolean;
  nameFirst?: string;
  nameSecond?: string;
  oneTextareaName?: string;
  placeholderFirst?: string;
  placeholderSecond?: string;
  name?: string;
  placeholder?: string;
  panelKey?: string;
  children?: any;
};

const examinationCheckbox = [
  'Hepatitis/Liver Disease',
  'Aids or HIV positive',
  'Anemia',
  'Sleep Apnea',
  'Diabetes',
  'Cancer',
  'Elevated Cholesterol',
  'Alzheimer’s',
  'High Blood Pressure',
  'Heart Disease/ Murmur',
  'Asthma',
  'Seizures',
  'Thyroid Disease',
  'Dementia',
  'Parkinson’s Disease',
  'Other',
];

const planCheckbox = ['Conservative RX', 'intervention RX', 'Surgical', 'Preventive RX', 'Other'];

const examinationSystemCheckbox = [
  {
    title: 'Constitution',
    items: [
      {
        item: 'fevers',
        exist: false,
      },
      {
        item: 'loss of appetite',
        exist: true,
      },
      {
        item: 'night sweats',
        exist: true,
      },
      {
        item: 'weight loss',
        exist: false,
      },
      {
        item: 'weight gain',
        exist: false,
      },
    ],
  },
  {
    title: 'Cardiovascular',
    items: [
      {
        item: 'palpitations',
        exist: true,
      },
      {
        item: 'chest pain',
        exist: false,
      },
      {
        item: 'fainting',
        exist: false,
      },
      {
        item: 'legs swelling',
        exist: false,
      },
      {
        item: 'unable to lie flat',
        exist: false,
      },
    ],
  },
  {
    title: 'GASTROINTESTINAL',
    items: [
      {
        item: 'indigestion',
        exist: false,
      },
      {
        item: 'nausea',
        exist: false,
      },
      {
        item: 'vomiting',
        exist: false,
      },
    ],
  },
  {
    title: 'NEUROLOGIC',
    items: [
      {
        item: 'sleepiness',
        exist: false,
      },
      {
        item: 'sleepiness',
        exist: false,
      },
      {
        item: 'headaches',
        exist: true,
      },
      {
        item: 'dizziness',
        exist: false,
      },
      {
        item: 'numbness',
        exist: false,
      },
    ],
  },
];

const displayExaminationCheckbox = examinationCheckbox.map((item, i) => {
  return (
    <Col span={6} key={i}>
      <Checkbox>{item}</Checkbox>
    </Col>
  );
});

const displayPlanCheckbox = planCheckbox.map((item, i) => {
  return (
    <Col span={6} key={i}>
      <Checkbox>{item}</Checkbox>
    </Col>
  );
});

const { TextArea } = Input;
const { Option } = Select;

function PanelInputs({
  inputsBeside,
  inputLine,
  oneTextareaHolder,
  status,
  inputsLine,
  formTitle,
  showExaminationCheckbox,
  formTitleContent,
  showPlanCheckbox,
  inputsLineContentOne,
  inputsLineContentTwo,
  otherTextarea,
  saveChangeBtn,
  showExaminationsySystemCheckboc,
  referral,
  Onetextarea,
  nameFirst,
  nameSecond,
  oneTextareaName,
  placeholderFirst,
  placeholderSecond,
  name,
  placeholder,
  panelKey,
  children,
}: Iprops) {
  const getRedFlag = (items) => {
    let redFlag = items.filter((item) => item.exist);
    if (redFlag.length) {
      return 'umi-change-icon';
    }
  };

  return (
    <div className="panel-input-wrapper">
      {/* <MoreButton panelKey={panelKey} /> */}
      {/* <Handwriting /> */}
      {formTitle && <div className="umi-form-title">{formTitleContent}</div>}
      {showExaminationCheckbox && (
        <div className="umi-examination-checkbox">
          <Row gutter={[10, 10]}>{displayExaminationCheckbox}</Row>
        </div>
      )}
      {showPlanCheckbox && (
        <div className="umi-examination-checkbox">
          <Row gutter={[10, 10]}>{displayPlanCheckbox}</Row>
        </div>
      )}
      {showExaminationsySystemCheckboc && (
        <Row gutter={[10, 10]}>
          {examinationSystemCheckbox.map((item, i) => {
            return (
              <Col span={6} key={i}>
                <div className="umi-system-review">
                  <div className="umi-review-tilte-wrapper">
                    <div className={`umi-icon-wrapper ${getRedFlag(item.items)}`}>
                      <FlagOutlined />
                    </div>
                    <div className="umi-review-title">{item.title}</div>
                  </div>
                  {item.items.map((box, i) => {
                    return (
                      <Checkbox
                        key={i}
                        defaultChecked={box.exist}
                        onChange={(event) => (box.exist = event.target.checked)}
                      >
                        {box.item}
                      </Checkbox>
                    );
                  })}
                </div>
              </Col>
            );
          })}
        </Row>
      )}
      {inputsBeside && (
        <Row gutter={15}>
          <Col span="12">
            <UmiInputs name={nameFirst} placeholder={placeholderFirst} />
          </Col>
          <Col span="12">
            <UmiInputs name={nameSecond} placeholder={placeholderSecond} />
          </Col>
        </Row>
      )}
      {/* {inputLine && <Input placeholder="Red Eye" />} */}
      {inputLine && <UmiInputs name={name} placeholder={placeholder} />}
      {inputsLine && (
        <>
          <Input placeholder={inputsLineContentOne} />
          <Input placeholder={inputsLineContentTwo} />
        </>
      )}
      {Onetextarea && <UmiTextarea name={oneTextareaName} placeholder={oneTextareaHolder} />}
      {otherTextarea && <TextArea rows={4} className="other-textarea" placeholder="Operative notes" />}
      {status && (
        <div className="umi-status">
          <div className="umi-status-title">Status</div>
          <div className="umi-status-data">
            <Checkbox>Approved</Checkbox>
            <div>Dr. Ahmed Hassan</div>
            <div>23 June 2019</div>
          </div>
        </div>
      )}
      {saveChangeBtn && <button className="umi-btn small">Save changes</button>}
      {referral && (
        <div className="umi-referral-form">
          <Input placeholder="Speciality" />
          <Select defaultValue="Referral doctor" style={{ width: '100%' }}>
            <Option value="critical-cases">Critical Cases</Option>
            <Option value="quick-reservation">Quick Reservation</Option>
            <Option value="reservation-descending">Reservation Descending</Option>
            <Option value="reservation-ascending">Reservation Ascending</Option>
          </Select>
          <div className="umi-external-referral">
            <Checkbox>External Referral</Checkbox>
            <Input placeholder="Doctor Name Or URL" />
          </div>
        </div>
      )}
      {children}
    </div>
  );
}

export default PanelInputs;
