import React, { useState } from "react";
import { Radio, Form, Input } from "antd";
import "./AuthInputRadio.less";
type Iprops = {
  inputRadio?: any;
  radioLabel?: string;
  radioOptions?: any;
  radioName?: string;
};

function AuthInputRadio({ radioLabel, radioOptions, radioName }: Iprops) {
  const [customRadio, setCustomRadio] = useState("ee");

  return (
    <div className="auth-input-radio">
      <Form.Item label={radioLabel} name={radioName}>
        <Radio.Group>
          {radioOptions && (
            <>
              {radioOptions.map((option) => (
                <React.Fragment key={option.value}>
                  {option.data === "custom" ? (
                    <Radio.Button value={customRadio} className="custom-radio">
                      <Input
                        placeholder="custom"
                        onChange={(e) => {
                          setCustomRadio(e.target.value);
                        }}
                      />
                    </Radio.Button>
                  ) : (
                    <Radio.Button value={option.value}>
                      {option.data}
                    </Radio.Button>
                  )}
                </React.Fragment>
              ))}
            </>
          )}
        </Radio.Group>
      </Form.Item>
    </div>
  );
}
export default AuthInputRadio;
