import React from 'react';
import { BrowserRouter as Router, Route, Redirect, Switch } from 'react-router-dom';

import Login from '../Components/Auth/AuthMainProcess/AuthLogin/Login';
import Signup from '../Components/Auth/AuthMainProcess/AuthSignup/Signup';
import SignupOptions from '../Components/Auth/AuthMainProcess/AuthSignup/SignupOptions/SignupOptions';
import VerifyRegistration from '../Components/Auth/AuthMainProcess/AuthSignup/VerifyRegistration';
import ResetPassword from '../Components/Auth/AuthMainProcess/ForgetVerifyProcess/ResetPassword/AuthResetPassword';
import ForgetPasswordPhone from '../Components/Auth/AuthMainProcess/ForgetVerifyProcess/ForgetPassword/ForgetPasswordPhone';
import ForgetPasswordEmail from '../Components/Auth/AuthMainProcess/ForgetVerifyProcess/ForgetPassword/ForgetPasswordEmail';
import VerifyPasswordPhone from '../Components/Auth/AuthMainProcess/ForgetVerifyProcess/VerifyPassword/VerifyPasswordPhone';
import VerifyPasswordEmail from '../Components/Auth/AuthMainProcess/ForgetVerifyProcess/VerifyPassword/VerifyPasswordEmail';

function AuthRoutes() {
  return (
    <div>
      <Router>
        <Switch>
          <Route path='/login' component={Login} />
          <Route path='/signup' component={Signup} />
          <Route path='/signup-options' component={SignupOptions} />
          <Route path='/verify-registration' component={VerifyRegistration} />
          <Route path='/forget-password-phone' component={ForgetPasswordPhone} />
          <Route path='/forget-password-email' component={ForgetPasswordEmail} />
          <Route path='/verify-password-phone' component={VerifyPasswordPhone} />
          <Route path='/verify-password-email' component={VerifyPasswordEmail} />
          <Route path='/reset-password' component={ResetPassword} />
          {/* <Redirect to='/login' /> */}
        </Switch>
      </Router>
    </div>
  );
}

export default AuthRoutes;
