import React, { useState, useEffect, useContext } from 'react';
import { Select } from 'antd';
import { FormContext } from '../AuthProfileComplete';

import AuthAddMoreDynamicForm from '../Inputs/AuthAddMoreDynamicForm/AuthAddMoreDynamicForm';
import { getMainSpecialty, getSubSpecialty } from '../Services/Specialty';

const { Option } = Select;

type IProps = {
  selectedSpecialty: Function;
  selectedSubSpecialty: Function;
};

function AuthMainSubSpecialities({ selectedSpecialty, selectedSubSpecialty }: IProps) {
  const [mainSpecialty, setMainSpecialty] = useState([]);
  const [subSpecoalityData, setSubSpecoalityData] = useState([] as any);

  const form = useContext(FormContext);

  useEffect(() => {
    getMainSpecialty('business1').then(({ data }) => setMainSpecialty(data.data));
  }, []);

  const getSubSpecialtyData = (specialtyId: any) => {
    getSubSpecialty(specialtyId).then(({ data }) => setSubSpecoalityData(data.data));
  };

  const handleMainSpecialty = (value) => {
    mainSpecialty.map((specialty: any) => {
      if (specialty.mainSpeciality === value) {
        getSubSpecialtyData(specialty._id);
      }
    });
    selectedSpecialty(form.getFieldValue('professionalExperienceMainSubSpeciality'));
  };
  const handleSubSpecialty = () => {
    selectedSubSpecialty(form.getFieldValue('professionalExperienceMainSubSpeciality'));
  };

  const professionalExperienceMainSpecialityData = [
    {
      formItemName: 'mainSpecialty',
      formItemLayout: '11',
      formItemLabel: 'Main Specialty',
      key: 4,
      dynamicContent: (
        <Select showSearch={true} onChange={handleMainSpecialty}>
          {mainSpecialty.length > 0 &&
            mainSpecialty.map((special: any) => (
              <Option key={special._id} value={special.mainSpeciality}>
                {special.mainSpeciality}
              </Option>
            ))}
        </Select>
      ),
    },
    {
      formItemName: 'subSpecialty',
      formItemLayout: '11',
      formItemLabel: 'Subspecialty',
      key: 5,
      // dynamicContent: <AuthInputSelect inputSelectMultiple options={subSpecoalityData} />,
      dynamicContent: (
        <Select mode='multiple' onChange={handleSubSpecialty}>
          {subSpecoalityData.length > 0 &&
            subSpecoalityData.map((option) => (
              <Option key={option} value={option}>
                {option}
              </Option>
            ))}
        </Select>
      ),
    },
  ];

  return (
    <div>
      <AuthAddMoreDynamicForm
        formListName='professionalExperienceMainSubSpeciality'
        formItemData={professionalExperienceMainSpecialityData}
        backgroundStyle={'bg-add-new'}
      />
    </div>
  );
}

export default AuthMainSubSpecialities;
