import React, { Component } from 'react';
import { Descriptions } from 'antd';
import PatientData from './PatientData/PatientData';
import CheckboxData from './CheckboxData/CheckboxData';
import DoctorDataReport from './DoctorDataReport/DoctorDataReport';

import './UmiPrint.less';

type Iprops = {
  form: any;
  sendValue: any;
};

export default class UmiPrint extends Component<Iprops> {
  medicalHistory = ['Allergies', 'Medication', 'Surgeries', 'Vaccination', 'System Review', 'Other Spcialities'];

  others = ['Screening Tests', 'Personal', 'Family', 'Social History', 'Women History', 'Other Health Issues'];

  render() {
    const { form, sendValue } = this.props;
    const { getFieldValue } = form;
    return (
      <div className="umi-print">
        <DoctorDataReport />
        <PatientData title additionData />
        {sendValue['checkbox-group']! &&
          sendValue['checkbox-group']!.map((panelId: string, i) => (
            <div key={i}>
              {panelId === 'presenting-complain' && (
                <>
                  <div className="title">Presenting Complain</div>
                  <Descriptions className="presenting-data">
                    <Descriptions.Item label="Subject">{getFieldValue('subject')}</Descriptions.Item>
                    <Descriptions.Item label="ID">{getFieldValue('hash')}</Descriptions.Item>
                    <Descriptions.Item label="Description">{getFieldValue('description')}</Descriptions.Item>
                  </Descriptions>
                </>
              )}
              {panelId === 'medical-history' && (
                <>
                  <div className="title">Medical History</div>
                  {this.medicalHistory.map((item, i) => {
                    return (
                      <div key={i} className="umi-patient-examination-info">
                        <div className="umi-examination-type">{item}</div>
                        <div className="umi-examination-res">
                          <div className="umi-examination-status">Red eyes</div>
                          <div className="umi-examination-par">
                            Have you ever felt like you or someone you know is constantly coughing and sneezing? When a
                            cough isn't just a cough, it could be allergies. In fact, allergies
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </>
              )}
              {panelId === 'others' && (
                <>
                  <div className="title">Others</div>
                  {this.others.map((item, i) => {
                    return (
                      <div key={i} className="umi-patient-examination-info">
                        <div className="umi-examination-type">{item}</div>
                        <div className="umi-examination-res">
                          <div className="umi-examination-status">Red eyes</div>
                          <div className="umi-examination-par">
                            Have you ever felt like you or someone you know is constantly coughing and sneezing? When a
                            cough isn't just a cough, it could be allergies. In fact, allergies
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </>
              )}
              {panelId === 'history-review' && (
                <>
                  <div className="title">History Review</div>
                  <Descriptions className="presenting-data">
                    <Descriptions.Item label="History Complain">{getFieldValue('history-complain')}</Descriptions.Item>
                    <Descriptions.Item label="History Description">
                      {getFieldValue('history-description')}
                    </Descriptions.Item>
                  </Descriptions>
                </>
              )}
              {panelId === 'allergies' && (
                <>
                  <div className="title">Allergies</div>
                  <Descriptions className="presenting-data">
                    <Descriptions.Item label="Subject">{getFieldValue('allergies-subject')}</Descriptions.Item>
                    <Descriptions.Item label="ID">{getFieldValue('allergies-id')}</Descriptions.Item>
                    <Descriptions.Item label="Medicine">{getFieldValue('allergies-medicine')}</Descriptions.Item>
                  </Descriptions>
                </>
              )}
              {panelId === 'personal' && (
                <>
                  <div className="title">Personal</div>
                  <CheckboxData />
                </>
              )}
              {panelId === 'family' && (
                <>
                  <div className="title">Family</div>
                  <CheckboxData />
                </>
              )}
              {panelId === 'system-review' && (
                <>
                  <div className="title">System Review</div>
                  <CheckboxData />
                </>
              )}
              {panelId === 'diagnosis' && (
                <>
                  <div className="title">Diagnosis</div>
                  <Descriptions className="presenting-data">
                    <Descriptions.Item label="Subject">{getFieldValue('diagnosis-subject')}</Descriptions.Item>
                    <Descriptions.Item label="ID">{getFieldValue('diagnosis-id')}</Descriptions.Item>
                    <Descriptions.Item label="Main Complain">{getFieldValue('diagnosis-complain')}</Descriptions.Item>
                  </Descriptions>
                </>
              )}
              {panelId === 'plan' && (
                <>
                  <div className="title">Plan</div>
                  <CheckboxData />
                </>
              )}
              {panelId === 'medication-prescription' && <h1>Medication Prescription</h1>}
              {panelId === 'laboratory-prescription' && <h1>Laboratory Prescription</h1>}
              {panelId === 'radiology-prescription' && <h1>Radiology Prescription</h1>}
              {panelId === 'consumable-prescription' && <h1>Consumable Prescription</h1>}
              {panelId === 'vaccination-prescription' && <h1>Vaccination Prescription</h1>}
              {panelId === 'surgeries' && (
                <>
                  <div className="title">Surgeries</div>
                  <Descriptions className="presenting-data">
                    <Descriptions.Item label="Surgery Name">{getFieldValue('surgery-name')}</Descriptions.Item>
                    <Descriptions.Item label="Recommended Duration">
                      {getFieldValue('surgeries-duration')}
                    </Descriptions.Item>
                    <Descriptions.Item label="Surgical Procedure">
                      {getFieldValue('surgical-procedure')}
                    </Descriptions.Item>
                    <Descriptions.Item label="Operative Notes">
                      {getFieldValue('surgeries-operative')}
                    </Descriptions.Item>
                  </Descriptions>
                </>
              )}
              {panelId === 'bed-side-maneuvers' && (
                <>
                  <div className="title">Bed-side Maneuvers</div>
                  <Descriptions className="presenting-data">
                    <Descriptions.Item label="Needed Maneuvers">{getFieldValue('needed-maneuvers')}</Descriptions.Item>
                    <Descriptions.Item label="Recommended Duration">
                      {getFieldValue('maneuvers-duration')}
                    </Descriptions.Item>
                    <Descriptions.Item label="Notes">{getFieldValue('maneuvers-notes')}</Descriptions.Item>
                  </Descriptions>
                </>
              )}
              {panelId === 'referral' && <h1>Referral</h1>}
              {panelId === 'medical-recommendations' && (
                <>
                  <div className="title">Medical Recommendations</div>
                  <Descriptions className="presenting-data">
                    <Descriptions.Item label="Subject">{getFieldValue('recommendations-subject')}</Descriptions.Item>
                    <Descriptions.Item label="ID">{getFieldValue('recommendations-id')}</Descriptions.Item>
                    <Descriptions.Item label="Notes">{getFieldValue('recommendations-notes')}</Descriptions.Item>
                  </Descriptions>
                </>
              )}
              {panelId === 'report' && <h1>Report</h1>}
            </div>
          ))}
      </div>
    );
  }
}
