import React from "react";
import axios from "axios";
import { Form, message } from "antd";
import { useHistory } from "react-router-dom";
import ForgetVerifyProcess from "../ForgetVerifyProcess";

function ResetPassword() {
  const history = useHistory();
  const handleResetPassword = (newPasswordValue) => {
    console.log(newPasswordValue);
    const headerData = {
      Authorization: `Bearer ${localStorage.getItem("changeToken")}`,
    };

    axios({
      method: "PATCH",
      url: "/u-consultation/auth/change-password",
      data: newPasswordValue,
      headers: headerData,
    })
      .then((response) => {
        console.log(response);
        history.push("/login");
      })
      .catch((error) => {
        console.log(error);
        if (error.response) {
          console.log(error.response.data.message);
        } else {
          message.error("An error occurred. Please try again.");
        }
      });
  };
  return (
    <div className="reset-password">
      <Form onFinish={handleResetPassword}>
        <ForgetVerifyProcess
          head="Reset Password"
          descreption="Please enter your New Password"
          contentInputPassword
          button="confirm"
        />
      </Form>
    </div>
  );
}
export default ResetPassword;
