import React from "react";
import { Row, Col } from "antd";
import AuthInput from "../../../Inputs/AuthInput/AuthInput";

function AuthPersonalInfoInterests() {
  return (
    <div>
      <Row justify="space-between" className="bg-add-new">
        <Col span={11}>
          <AuthInput
            textArea
            textAreaName="interests"
            textAreaLabel="Interests"
            textAreaPlaceholder="Add your interests and separate with a comma"
          />
        </Col>
        <Col span={11}>
          <AuthInput
            textArea
            textAreaName="professionalInterests"
            textAreaLabel="Professional Interests"
            textAreaPlaceholder="Add your interests and separate with a comma"
          />
        </Col>
        <AuthInput checkBox checkBoxData="Share this info with network" />
      </Row>
    </div>
  );
}

export default AuthPersonalInfoInterests;
