import React, { useContext } from "react";
import { Tabs } from "antd";
import AuthPersonnelLicensure from "./AuthPersonnelLicensure";
import AuthLicensureMedicalFacility from "./AuthLicensureMedicalFacility";
import { TabContext } from "../AuthProfileComplete";
const { TabPane } = Tabs;

function AuthLicensure() {
  const activeTab = useContext(TabContext);
  return (
    <div>
      <Tabs activeKey={activeTab}>
        <TabPane tab="Key Personnel" key="1">
          <AuthPersonnelLicensure />
        </TabPane>
        <TabPane tab="Medical Facilities" key="2">
          <AuthLicensureMedicalFacility />
        </TabPane>
      </Tabs>
    </div>
  );
}

export default AuthLicensure;
