import React from 'react';
import { Collapse, Checkbox } from 'antd';
import UmiInputs from '../../Common/PanelInputs/UmiInputs';
import UmiTextarea from '../../Common/PanelInputs/UmiTextarea';
import PanelInputs from '../../Common/PanelInputs/PanelInputs';
import ExportExcel from '../../Common/ExportExcel/ExportExcel';
import PanelControl from '../../Common/PanelControl/PanelControl';
import SearchInput from '../../Common/PanelInputs/SearchInput/SearchInput';

import './Examination.less';

const { Panel } = Collapse;

function Examination() {
  const ExportExcelRender = () => <ExportExcel activeTab="examination" tabName="Examination" />;

  return (
    <Collapse defaultActiveKey={['history-review']} bordered={false}>
      <Panel header="History Review" key="history-review">
        <PanelControl moreButton panelKey="history-review">
          <ExportExcelRender />
        </PanelControl>
        <PanelInputs>
          <SearchInput name="history-complain" placeholder="Red Eye" />
          <UmiTextarea
            name="history-description"
            placeholder="Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet"
          />
          <div className="umi-status">
            <div className="umi-status-title">Status</div>
            <div className="umi-status-data">
              <Checkbox>Approved</Checkbox>
              <div>Dr. Ahmed Hassan</div>
              <div>23 June 2019</div>
            </div>
          </div>
        </PanelInputs>
      </Panel>
      <Panel header="Allergies" key="allergies">
        <PanelControl moreButton panelKey="allergies">
          <ExportExcelRender />
        </PanelControl>
        <PanelInputs>
          <SearchInput name="allergies-subjec" placeholder="Subject" />
          <UmiInputs type="number" name="allergies-id" placeholder="#" />
          <UmiTextarea name="allergies-medicine" placeholder="Medicine name …" />
        </PanelInputs>
      </Panel>
      <Panel header="Personal" key="personal">
        <PanelControl moreButton panelKey="personal">
          <ExportExcelRender />
        </PanelControl>
        <PanelInputs formTitle formTitleContent="Does patient suffer from:" showExaminationCheckbox>
          <UmiTextarea name="personal-notes" placeholder="Notes" />
        </PanelInputs>
      </Panel>
      <Panel header="Family" key="family">
        <PanelControl moreButton panelKey="family">
          <ExportExcelRender />
        </PanelControl>
        <PanelInputs
          formTitle="formTitle"
          formTitleContent="Do any family members suffer from:"
          showExaminationCheckbox="showExaminationCheckbox"
        >
          <UmiTextarea name="family-notes" placeholder="Notes" />
        </PanelInputs>
      </Panel>
      <Panel header="System Review" key="system-review" className="umi-system-review">
        <PanelControl moreButton panelKey="system-review">
          <ExportExcelRender />
        </PanelControl>
        <PanelInputs showExaminationsySystemCheckboc />
      </Panel>
    </Collapse>
  );
}

export default Examination;
