import React, { useEffect, useState, useContext } from "react";
import { Form } from "antd";
import PhoneInput from "react-phone-input-2";
import { Iprops } from "./AuthInputsTypes";

import "react-phone-input-2/lib/style.css";
import { SignupFormContext } from "../../AuthSignup/SignupOptions/SignupOptions";

function InputPhone({
  inputPhone,
  inputPhoneValidator,
  phoneValidatorMessage,
}: Iprops) {
  const [phone, setPhone] = useState("");
  const signupFormContext = useContext(SignupFormContext);

  useEffect(() => {
    signupFormContext.setFieldsValue({ phoneNumber: `${phone}` });
    console.log(signupFormContext.getFieldValue("phoneNumber"));
  });
  return (
    <div>
      {inputPhone && (
        <Form.Item
          name="phoneNumber"
          rules={[
            {
              validator(rule, value) {
                const reg = new RegExp(`${inputPhoneValidator}`);
                if (reg.test(value)) {
                  return Promise.resolve();
                }
                return Promise.reject(`${phoneValidatorMessage}`);
              },
            },
          ]}
        >
          <PhoneInput
            country={"eg"}
            onChange={(phone) => {
              setPhone(phone);
            }}
          />
        </Form.Item>
      )}
    </div>
  );
}

export default InputPhone;
