import React, { useState, createContext } from 'react';
import Fullscreen from 'react-full-screen';
import { withRouter } from 'react-router-dom';
import { UserOutlined, CloseOutlined } from '@ant-design/icons';
import { Row, Col, Tabs, DatePicker, Avatar, Badge, Form } from 'antd';
import Report from './Report/Report';
import Examination from './Examination/Examination';
import Eprescription from './Eprescription/Eprescription';
import DiagnosisPlan from './DiagnosisPlan/DiagnosisPlan';
import SessionHistory from './SessionHistory/SessionHistory';
import CountdownTime from '../Common/CountdownTime/CountdownTime';
import Reservation from '../ClinicViewStart/Reservation/Reservation';
import PatientInformation from './PatientInformation/PatientInformation';
import PatientReservation from '../Common/PatientReservation/PatientReservation';
import InterventionsReferrals from './InterventionsReferrals/InterventionsReferrals';

import './PatientDetalis.less';

const { TabPane } = Tabs;
const { RangePicker } = DatePicker;

function onChange(dates, dateStrings) {
  console.log('From: ', dates[0], ', to: ', dates[1]);
  console.log('From: ', dateStrings[0], ', to: ', dateStrings[1]);
}

export const FormContext = createContext({} as any);

function PatientDetalis() {
  const [isFull, setFullScreen] = useState(false);
  const [showReservation, setShowReservation] = useState(false);
  const [activeKey, setActiveKey] = useState('eprescription');
  const [patientInfo, setPatientInfo] = useState();
  const [form] = Form.useForm();

  const callbackSession = (childData) => setActiveKey(childData);

  const onFinish = (values) => {
    console.log('Success:', values);
  };

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };

  return (
    <Fullscreen enabled={isFull} onChange={(isFull) => setFullScreen(isFull)}>
      <div className="umi-session-history-wrapper">
        <div className="umi-reservation-popup-wrapper">
          {showReservation && <Reservation />}
          <div
            className={`umi-reservation-popup ${showReservation && 'umi-display-reservation'}`}
            onClick={showReservation ? () => setShowReservation(false) : () => setShowReservation(true)}
          >
            <span className="avatar-item">
              <Badge count={18}>
                <Avatar shape="square" icon={showReservation ? <CloseOutlined /> : <UserOutlined />} />
              </Badge>
            </span>
          </div>
        </div>
        <div className="umi-container">
          <div className="umi-patient-session-info">
            <Row>
              <Col span="16">
                <PatientReservation critical="critical" score="score" id="id" flag="flag" controls="controls" />
              </Col>
              <Col span="8">
                <CountdownTime time={900000} title="Examination time" />
              </Col>
            </Row>
          </div>
          <div className="umi-clinic-tabs">
            <Form name="basic" form={form} initialValues={{ remember: true }}>
              <FormContext.Provider value={form}>
                <Tabs activeKey={activeKey} onChange={(activeKey) => setActiveKey(activeKey)}>
                  <TabPane tab="Session history" className="umi-session-history" key="session-history">
                    <SessionHistory sendText={callbackSession} />
                  </TabPane>
                  <TabPane tab="Patient information" key="patient-information">
                    <PatientInformation />
                  </TabPane>
                  <TabPane tab="Examination" key="examination">
                    <Examination />
                  </TabPane>
                  <TabPane tab="Diagnosis & plan" key="diagnosis-plan">
                    <DiagnosisPlan />
                  </TabPane>
                  <TabPane tab="E-prescription" key="eprescription">
                    <Eprescription />
                  </TabPane>
                  <TabPane tab="Interventions & Referrals" key="interventions-referrals">
                    <InterventionsReferrals />
                  </TabPane>
                  <TabPane tab="Report" key="report">
                    <Report />
                  </TabPane>
                </Tabs>
              </FormContext.Provider>
            </Form>
          </div>
        </div>
      </div>
    </Fullscreen>
  );
}

export default withRouter(PatientDetalis);
