import React from 'react';
import AuthRoutes from './AuthRoutes';
import CompleteSignupRoutes from './CompleteSignupRoutes';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import ClinicView from '../Components/ClinicView/ClinicView';
import PatientDetalis from '../Components/ClinicView/PatientDetails/PatientDetalis';
import ClinicViewStart from '../Components/ClinicView/ClinicViewStart/ClinicViewStart';
import Login from '../Components/Auth/AuthMainProcess/AuthLogin/Login';

const isAuthenticated = localStorage.getItem('user');

function Routes() {
  return (
    <Router>
      <Switch>
        <Route exact path='/' component={Login} />
        <ClinicView>
          <Route path='/clinic-view' component={ClinicViewStart} />
          <Route path='/patient-details' component={PatientDetalis} />
        </ClinicView>
      </Switch>
      <div>{isAuthenticated ? <CompleteSignupRoutes /> : <AuthRoutes />}</div>
    </Router>
  );
}

export default Routes;
