import React, { useState } from "react";
import axios from "axios";
import { Form, message } from "antd";
import Countdown from "react-countdown";
import { useHistory } from "react-router-dom";

import ForgetVerifyProcess from "../ForgetVerifyProcess";

function VerifyPasswordPhone() {
  const history = useHistory();
  const [timerStatus, setTimerStatus] = useState(true);
  const handleForgetPasswordPhoneConfirmation = (
    forgetPasswordConfirmation
  ) => {
    const otpData = {
      type: "OTP",
      username: localStorage.getItem("forgetPhoneNumber"),
      password: forgetPasswordConfirmation,
    };
    // verifyPasswordService(otpData, history);
    axios({
      method: "POST",
      url: "/u-consultation/auth/login",
      data: otpData,
    })
      .then((response) => {
        console.log(response);
        console.log(response.data.data);
        localStorage.setItem("changeToken", response.data.data);
        history.push("/reset-password");
      })
      .catch((error) => {
        console.log(error);
        if (error.response) {
          if (error.response.data.message === "Invalid Otp") {
            message.error("Invalid Code  ! try again");
          }
          console.log(error.response.data.message);
        } else {
          message.error("An error occurred. Please try again.");
        }
      });
  };

  const handleRevrefiyPhone = () => {
    axios({
      method: "POST",
      url: "/u-consultation/auth/resend-phone-otp",
      data: localStorage.getItem("forgetPhoneNumber"),
    })
      .then((response) => {
        console.log(response);
      })
      .catch((error) => {
        console.log(error);
        if (error.response) {
          console.log(error.response.data.message);
        } else {
          message.error("An error occurred. Please try again.");
        }
      });
  };
  const renderer = ({ hours, minutes, seconds, completed }) => {
    if (completed) {
      setTimerStatus(false);
      return <div></div>;
    } else {
      return (
        <span>
          {hours}:{minutes}:{seconds}
        </span>
      );
    }
  };
  return (
    <Form>
      <div className="verify">
        <ForgetVerifyProcess
          head="Forgot Password"
          descreption="Please enter the pin sent to your phone number "
          button="Submit"
          contentCode
          onComplete={handleForgetPasswordPhoneConfirmation}
          footer="Reset password with your verified email address."
          footerlink="/verify-password-email"
          extraData="Didn't Receive SMS? "
          extraDataLink={
            <span>
              {timerStatus === true ? (
                <div>
                  <span>Resend in </span>
                  <Countdown
                    date={Date.now() + 1000 * 90}
                    renderer={renderer}
                  />
                </div>
              ) : (
                <div
                  onClick={() => {
                    setTimerStatus(true);
                    handleRevrefiyPhone();
                  }}
                >
                  click here to resend
                </div>
              )}
            </span>
          }
        />
      </div>
    </Form>
  );
}
export default VerifyPasswordPhone;
