import React, { useState, useContext } from 'react';
import { Modal, Button, DatePicker, InputNumber } from 'antd';
import { PhoneObj } from './PhoneObj';
import { LocationObj } from './LocationObj';
import Map from '../../Modals/Map/Map';
import AuthInput from '../../Inputs/AuthInput/AuthInput';
import { FormContext } from '../../AuthProfileComplete';

import { facilityName, facilities, addBranches } from '../FacilityContext';

import AuthInputSelect from '../../Inputs/AuthInputSelect';
import AuthInputUpload from '../../Inputs/AuthUpload/AuthInputUpload';
import AuthAddMoreDynamicForm from '../../Inputs/AuthAddMoreDynamicForm/AuthAddMoreDynamicForm';
import '../../Modals/Modal.less';
import './AuthMedicalFacility.less';

const phoneObj = PhoneObj;
const locationObj = LocationObj;

function AuthMedicalFacilityBranch() {
  const form = useContext(FormContext);

  const addBrachName = useContext(addBranches);

  const facilitiesNames: facilityName[] = useContext(facilities);
  const [facilitiesOptions, setFacilitesNames] = useState<facilityName[]>([]);
  const [modalStatus, setModalStatus] = useState({ visible: false });
  const getFacilities = () => {
    setFacilitesNames([...facilitiesNames]);
  };
  const getAddress = () => {
    setModalStatus({ visible: true });
  };
  const handleSubmitModal = () => {
    setModalStatus({
      visible: false,
    });
  };

  const handleCancelModal = () => {
    setModalStatus({
      visible: false,
    });
  };
  const medicalFacilityBranchItemData = [
    {
      formItemName: 'upload',
      formItemLayout: '24',
      dynamicContent: <AuthInputUpload />,
      key: 0,
    },

    {
      formItemName: 'facilityName',
      formItemLabel: 'Facility Name',
      formItemLayout: '24',
      dynamicContent: (
        <div onClick={getFacilities}>
          <AuthInputSelect options={facilitiesOptions} inputSelect inputSelectPlaceholder='Facility Name' />
        </div>
      ),
      key: 111,
    },

    {
      formItemName: 'branchName',
      formItemLabel: 'Branch Name',
      formItemLayout: '11',
      dynamicContent: (
        <div>
          <AuthInput inputText onBlur={addBrachName} inputTextName='BranchName' />

          <AuthInput inputText inputTextName='BranchName' />
        </div>
      ),
      key: 2,
    },
    {
      formItemName: 'arabicBranchName',
      formItemLabel: 'Branch Name (In Arabic)',
      formItemLayout: '11',
      dynamicContent: (
        <div>
          <AuthInput inputText inputTextName='BranchName' />
        </div>
      ),
      key: 2222,
    },

    {
      formItemName: 'commercialRegisterId',
      formItemLabel: 'Commercial Register ID No.',
      formItemLayout: '11',
      dynamicContent: (
        <div>
          <AuthInput inputText inputTextName='commercial' />
        </div>
      ),
      key: 5,
    },
    {
      formItemName: 'taxId',
      formItemLabel: 'Tax Id Number',
      formItemLayout: '11',
      dynamicContent: (
        <div>
          <InputNumber />
        </div>
      ),
      key: 5555,
    },
    {
      formItemName: 'dateEstablished',
      formItemLabel: 'Date Established',
      formItemLayout: '11',
      dynamicContent: <DatePicker />,
      key: 4,
    },
    {
      formItemName: 'branchLocation',
      formItemLabel: 'Address',
      formItemLayout: '11',
      dynamicContent: (
        <div>
          <AuthInput inputText inputTextName='address' onClick={getAddress} />
        </div>
      ),
      key: 7,
    },
  ];
  const medicalFacilityBranchAllData: any = [];
  medicalFacilityBranchAllData.push(...medicalFacilityBranchItemData);
  medicalFacilityBranchAllData.push(...locationObj);
  medicalFacilityBranchAllData.push(...phoneObj);

  return (
    <div>
      <AuthAddMoreDynamicForm
        backgroundStyle='bg-add-new'
        formListName='facilityBranch'
        formItemData={medicalFacilityBranchAllData}
      />
      <Modal visible={modalStatus.visible} className='custom-modal'>
        <Map />
        <div className='modal-buttons'>
          <Button onClick={handleSubmitModal}>Confirm Location</Button>
          <Button onClick={handleCancelModal}>Cancel</Button>
        </div>
      </Modal>
    </div>
  );
}
export default AuthMedicalFacilityBranch;
