import React, { useState } from 'react';
import Fullscreen from 'react-full-screen';
// import ClinicViewHeader from './Header/Header';

function ClinicView({ children }) {
  const [isFull, setFullScreen] = useState(false);

  return (
    <Fullscreen enabled={isFull} onChange={(isFull) => setFullScreen(isFull)}>
      {/* <ClinicViewHeader setFullScreen={(isFull) => setFullScreen(isFull)} clinicView /> */}
      {children}
    </Fullscreen>
  );
}

export default ClinicView;
