import React from "react";
import { Select } from "antd";
import AuthInput from "../../../Inputs/AuthInput/AuthInput";
import AuthAddMoreDynamicForm from "../../../Inputs/AuthAddMoreDynamicForm/AuthAddMoreDynamicForm";
import "./AuthPersonallInfoEmail.less";

const { Option } = Select;

function AuthPersonalInfoEmail() {
  const items = ["personal", "organization", "other"];

  const personnallEmailItemData = [
    {
      formItemName: "emailType",
      formItemLabel: "Email Type",
      formItemLayout: "8",
      dynamicContent: (
        <Select placeholder="Email Type">
          {items.map((item) => (
            <Option key={item} value={item}>
              {item}
            </Option>
          ))}
        </Select>
      ),
      key: 0,
    },
    {
      formItemName: "email",
      formItemLabel: "Email Address",
      formItemRules: [{ type: "email" }],
      formItemLayout: "15",
      dynamicContent: (
        <div>
          <AuthInput inputEmail inputEmailName="email" />
        </div>
      ),
      key: 1,
    },
  ];
  return (
    <div className="auth-personal-info-email">
      <AuthAddMoreDynamicForm
        formListName="emails"
        formItemData={personnallEmailItemData}
        backgroundStyle="bg-add-new"
      />
    </div>
  );
}

export default AuthPersonalInfoEmail;
