import React, { useState, useContext, useRef, Fragment } from 'react';
import { MoreOutlined } from '@ant-design/icons';
import { useReactToPrint } from 'react-to-print';
import { Menu, Dropdown, Form, Modal, Checkbox } from 'antd';
import UmiPrint from '../UmiPrint/UmiPrint';
import { FormContext } from '../../PatientDetails/PatientDetalis';

type IProps = {
  panelKey?: string;
};

const CheckboxPrint = [
  {
    id: 1,
    value: 'presenting-complain',
    label: 'Presenting Complain',
  },
  {
    id: 2,
    value: 'medical-history',
    label: 'Medical History',
  },
  {
    id: 3,
    value: 'others',
    label: 'Others',
  },
  {
    id: 4,
    value: 'history-review',
    label: 'History Review',
  },
  {
    id: 5,
    value: 'allergies',
    label: 'Allergies',
  },
  {
    id: 6,
    value: 'personal',
    label: 'Personal',
  },
  {
    id: 7,
    value: 'family',
    label: 'Family',
  },
  {
    id: 8,
    value: 'system-review',
    label: 'System Review',
  },
  {
    id: 9,
    value: 'diagnosis',
    label: 'Diagnosis',
  },
  {
    id: 10,
    value: 'plan',
    label: 'Plan',
  },
  {
    id: 16,
    value: 'surgeries',
    label: 'Surgeries',
  },
  {
    id: 17,
    value: 'bed-side-maneuvers',
    label: 'Bed Side Maneuvers',
  },
  {
    id: 19,
    value: 'medical-recommendations',
    label: 'Medical Recommendations',
  },
];

function MoreButton({ panelKey }: IProps) {
  const form = useContext(FormContext);
  const [printForm] = Form.useForm();
  const componentRef = useRef<UmiPrint | null>(null);
  const [modalVisible, setModalVisible] = useState(false);
  const [printValue, setPrintValue] = useState({});

  const resetFields = () => {
    form.setFieldsValue({
      subject: undefined,
      hash: undefined,
      description: undefined,
    });
  };

  const printPanel = () => setModalVisible(true);

  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
    removeAfterPrint: true,
  });

  const acceptPrint = () => {
    setPrintValue(printForm.getFieldsValue());
    setModalVisible(false);
    setTimeout(() => {
      handlePrint!();
    });
  };

  const menu = (
    <Menu>
      <Menu.Item key="0">
        <a>Share Info</a>
      </Menu.Item>
      <Menu.Item key="1">
        <a onClick={resetFields}>Delete</a>
      </Menu.Item>
      <Menu.Item key="3">
        <a onClick={printPanel}>Print</a>
      </Menu.Item>
      <Menu.Item key="4">
        <a>Save As Favrite</a>
      </Menu.Item>
      <Menu.Item key="5">
        <a>Save As Template</a>
      </Menu.Item>
    </Menu>
  );

  return (
    <>
      <Dropdown overlay={menu} trigger={['click']}>
        <div className="more-icon-wrapper">
          <MoreOutlined />
        </div>
      </Dropdown>
      <Modal
        title="Select panel you want to print"
        centered
        visible={modalVisible}
        onOk={() => acceptPrint()}
        onCancel={() => setModalVisible(false)}
      >
        <Form name="print-form" form={printForm} initialValues={{ ['checkbox-group']: [panelKey] }}>
          <Form.Item name="checkbox-group">
            <Checkbox.Group>
              {CheckboxPrint.map((checkbox) => (
                <Fragment key={checkbox.id}>
                  <Checkbox value={checkbox.value}>{checkbox.label}</Checkbox>
                </Fragment>
              ))}
            </Checkbox.Group>
          </Form.Item>
        </Form>
      </Modal>
      <div style={{ display: 'none' }}>
        <UmiPrint ref={componentRef} form={form} sendValue={printValue} />
      </div>
    </>
  );
}

export default MoreButton;
