import React, { useContext } from 'react';
import { DeleteOutlined, EditOutlined } from '@ant-design/icons';
import PatientData from '../UmiPrint/PatientData/PatientData';
import DoctorDataReport from '../UmiPrint/DoctorDataReport/DoctorDataReport';
import { FormContext } from '../../PatientDetails/PatientDetalis';

import './EprescriptionReport.less';

type IProps = {
  medicalReport?: any;
  labReport?: any;
  radioReport?: any;
  consumReport?: any;
  vacReport?: any;
  passMedicines?: any;
  passLab?: any;
  passRadio?: any;
  passConsum?: any;
  editMedicineInfo?: Function;
  removeMedicineInfo?: Function;
  editLabInfo?: Function;
  removeLabInfo?: Function;
  editRadioInfo?: Function;
  removeRadioInfo?: Function;
  editConsumInfo?: Function;
  removeConsumInfo?: Function;
};

function EprescriptionReport({
  medicalReport,
  labReport,
  radioReport,
  consumReport,
  vacReport,
  passMedicines,
  passLab,
  passRadio,
  passConsum,
  editMedicineInfo,
  removeMedicineInfo,
  editLabInfo,
  removeLabInfo,
  editRadioInfo,
  removeRadioInfo,
  editConsumInfo,
  removeConsumInfo,
}: IProps) {
  const form = useContext(FormContext);

  const editMedicine = (index: number) => {
    form.setFieldsValue({
      'prescription-medicine': passMedicines[index]['prescription-medicine'],
      'medicine-capsules': passMedicines[index]['medicine-capsules'],
      'medicine-times': passMedicines[index]['medicine-times'],
      'medicine-weeks': passMedicines[index]['medicine-weeks'],
      'medicine-notes': passMedicines[index]['medicine-notes'],
    });
    editMedicineInfo && editMedicineInfo(index);
  };

  const editLab = (index: number) => {
    form.setFieldsValue({
      'prescription-laboratory': passLab[index]['prescription-laboratory'],
      'laboratory-notes': passLab[index]['laboratory-notes'],
    });
    editLabInfo && editLabInfo(index);
  };

  const editRadio = (index: number) => {
    form.setFieldsValue({
      'prescription-radiology': passRadio[index]['prescription-radiology'],
      'radiology-investigation': passRadio[index]['radiology-investigation'],
      'organ-scan': passRadio[index]['organ-scan'],
      'radiology-tincture': passRadio[index]['radiology-tincture'],
      'radiology-anesthesia': passRadio[index]['radiology-anesthesia'],
      'radiology-labs': passRadio[index]['radiology-labs'],
      'radiology-notes': passRadio[index]['radiology-notes'],
    });
    editRadioInfo && editRadioInfo(index);
  };

  const editConsum = (index: number) => {
    form.setFieldsValue({
      'prescription-consumable': passConsum[index]['prescription-consumable'],
      'consumable-notes': passConsum[index]['consumable-notes'],
    });
    editConsumInfo && editConsumInfo(index);
  };

  const removeMedicine = (index: number) => {
    removeMedicineInfo && removeMedicineInfo(index);
  };

  const removeLab = (index: number) => {
    removeLabInfo && removeLabInfo(index);
  };

  const removeRadio = (index: number) => {
    removeRadioInfo && removeRadioInfo(index);
  };

  const removeConsum = (index: number) => {
    removeConsumInfo && removeConsumInfo(index);
  };

  return (
    <div className='umi-medication-report'>
      <DoctorDataReport />
      <PatientData col={2} />
      <div className='umi-descripttion'>
        <div className='umi-description-title'>Rx:</div>
        <div className='umi-description-info'>
          {medicalReport && (
            <ul>
              {passMedicines.map((medicine, index: number) => (
                <li key={index}>
                  <div className='prescription-list'>
                    <span>- {medicine['prescription-medicine']}</span>
                    <span> - {medicine['medicine-capsules']} Capsules</span>
                    <span> {medicine['medicine-times']} Times</span>
                    <span> for {medicine['medicine-weeks']} Weeks</span>
                    {medicine['medicine-notes'] && <div>Notes: {medicine['medicine-notes']}</div>}
                  </div>
                  <div>
                    <span onClick={() => editMedicine(index)} className='umi-edit-medicine'>
                      <EditOutlined />
                    </span>
                    <span onClick={() => removeMedicine(index)} className='umi-remove-medicine'>
                      <DeleteOutlined />
                    </span>
                  </div>
                </li>
              ))}
            </ul>
          )}
          {labReport && (
            <ul>
              {passLab.map((lab, index: number) => (
                <li key={index}>
                  <div className='prescription-list'>
                    <span>- {lab['prescription-laboratory']}</span>
                    {lab['laboratory-notes'] && <div>Notes: {lab['laboratory-notes']}</div>}
                  </div>
                  <div>
                    <span onClick={() => editLab(index)} className='umi-edit-medicine'>
                      <EditOutlined />
                    </span>
                    <span onClick={() => removeLab(index)} className='umi-remove-medicine'>
                      <DeleteOutlined />
                    </span>
                  </div>
                </li>
              ))}
            </ul>
          )}
          {radioReport && (
            <ul>
              {passRadio.map((radio, index: number) => (
                <li key={index}>
                  <div className='prescription-list'>
                    <span>- {radio['prescription-radiology']}</span>
                    <span> - {radio['radiology-investigation']}</span>
                    {radio['organ-scans'] && <span> - {radio['organ-scan']}</span>}
                    {radio['radiology-tincture'] && <span> - {radio['radiology-tincture']}</span>}
                    {radio['radiology-anesthesia'] && <span> - {radio['radiology-anesthesia']}</span>}
                    {radio['radiology-labs'] && <span> - {radio['radiology-labs']}</span>}
                    {radio['radiology-notes'] && <div>Notes: {radio['radiology-notes']}</div>}
                  </div>
                  <div>
                    <span onClick={() => editRadio(index)} className='umi-edit-medicine'>
                      <EditOutlined />
                    </span>
                    <span onClick={() => removeRadio(index)} className='umi-remove-medicine'>
                      <DeleteOutlined />
                    </span>
                  </div>
                </li>
              ))}
            </ul>
          )}
          {consumReport && (
            <ul>
              {passConsum.map((consum, index: number) => (
                <li key={index}>
                  <div className='prescription-list'>
                    <span>- {consum['prescription-consumable']}</span>
                    {consum['consumable-notes'] && <div>Notes: {consum['consumable-notes']}</div>}
                  </div>
                  <div>
                    <span onClick={() => editConsum(index)} className='umi-edit-medicine'>
                      <EditOutlined />
                    </span>
                    <span onClick={() => removeConsum(index)} className='umi-remove-medicine'>
                      <DeleteOutlined />
                    </span>
                  </div>
                </li>
              ))}
            </ul>
          )}
          {vacReport && (
            <ul>
              <li>
                <span>Thyroid Disease</span> -<span> fasting required</span>
              </li>
              <li>
                <span>Thyroid Disease</span> -<span> fasting required</span>
              </li>
              <li>
                <span>Thyroid Disease</span> -<span> fasting required</span>
              </li>
            </ul>
          )}
        </div>
      </div>
    </div>
  );
}

export default EprescriptionReport;
