import React from 'react';

export type facilityName = {
  data: string;
  value: number;
};

export const facilitiesNames: facilityName[] = [];

export const branchesNames: facilityName[] = [];

export const addFacilityName = (event) => {
  let input = event.target;
  let name = input.value;
  const timeStamp = input.getAttribute('updated');
  if (timeStamp) {
    const facilityIndex = facilitiesNames.findIndex((f) => f.value == timeStamp);
    if (name == '') {
      facilitiesNames.splice(facilityIndex, 1);
      input.removeAttribute('updated');
      return;
    }
    facilitiesNames[facilityIndex].data = name;
    console.log('updated', facilitiesNames);
  } else {
    if (name == '') return;
    input.setAttribute('updated', Date.now());
    facilitiesNames.push({
      value: Date.now(),
      data: name,
    });
  }
};

export const addBranchName = (event) => {
  let input = event.target;
  let name = input.value;
  const timeStamp = input.getAttribute('updated');
  if (timeStamp) {
    const branchIndex = branchesNames.findIndex((f) => f.value == timeStamp);
    if (name == '') {
      branchesNames.splice(branchIndex, 1);
      input.removeAttribute('updated');
      return;
    }
    branchesNames[branchIndex].data = name;
  } else {
    if (name == '') return;
    input.setAttribute('updated', Date.now());
    branchesNames.push({
      value: Date.now(),
      data: name,
    });
  }
};

export const addFacilities = React.createContext(addFacilityName);
export const facilities = React.createContext(facilitiesNames);
export const branches = React.createContext(branchesNames);
export const addBranches = React.createContext(addBranchName);
