import React from "react";
import PhoneInput from "react-phone-input-2";
import AuthInput from "../../Inputs/AuthInput/AuthInput";
import AuthInputSelect from "../../Inputs/AuthInputSelect";
import { Input, InputNumber } from "antd";
export const PhoneObj = [
  {
    formItemName: "phoneType",
    formItemLabel: "Phone Type",
    formItemLayout: "8",
    dynamicContent: <AuthInputSelect inputSelect />,
    key: 9,
  },
  {
    formItemName: "phoneCode",
    formItemLabel: "Country Code",
    formItemLayout: "4",
    dynamicContent: (
      <div>
        <PhoneInput country={"eg"} onChange={(phone) => console.log(phone)} />
      </div>
    ),
    key: 10,
  },
  {
    formItemName: "phoneNumber",
    formItemLabel: "Phone Number",
    formItemLayout: "11",
    dynamicContent: (
      <div>
        <InputNumber />
      </div>
    ),
    key: 11,
  },
];
