import React from "react";
import { Modal, Form } from "antd";
import AuthAddMoreDynamicForm from "../../../../AuthCompleteProcess/Inputs/AuthAddMoreDynamicForm/AuthAddMoreDynamicForm";
import AuthInput from "../../../../AuthCompleteProcess/Inputs/AuthInput/AuthInput";

function InvitationFormModal({
  invitationFormVisible,
  handleOk,
  cancelInvitationModal,
}) {
  const initialValues = { inviteColleagues: [{}] };
  const inviteColleaguesFormItems = [
    {
      formItemName: "name",
      formItemLayout: "24",

      key: 1,
      dynamicContent: (
        <AuthInput inputText inputTextName="name" inputTextPlaceholder="Name" />
      ),
    },
    {
      formItemName: "email",
      formItemLayout: "24",

      key: 2,
      dynamicContent: (
        <AuthInput
          inputText
          inputTextName="Email"
          inputTextPlaceholder="Email"
        />
      ),
    },
    {
      formItemName: "phoneNumber",
      formItemLayout: "24",

      key: 3,
      dynamicContent: (
        <AuthInput
          inputText
          inputTextName="phoneNumber"
          inputTextPlaceholder="Phone Number"
        />
      ),
    },
  ];
  return (
    <div>
      <Modal
        visible={invitationFormVisible}
        onOk={handleOk}
        className="invitation-modal"
      >
        <h3>Invite Colleagues</h3>
        <p>
          Please fill the following fields with your colleague's info. Once at
          lease 10 of your colleagues register, you will redeem your free gold
          month.
        </p>
        <Form name="invite" initialValues={initialValues}>
          <AuthAddMoreDynamicForm
            backgroundStyle="bg-add-colleage "
            formListName="inviteColleagues"
            formItemData={inviteColleaguesFormItems}
          />
        </Form>
        <span className="modal-actions" onClick={cancelInvitationModal}>
          Cancel
        </span>
      </Modal>
    </div>
  );
}
export default InvitationFormModal;
