import React from 'react';
import { Checkbox } from 'antd';

import './CheckboxData.less';

function CheckboxData() {
  return (
    <ul className="checkbox-data-print">
      <li>
        <Checkbox checked>Diabetes</Checkbox>
      </li>
      <li>
        <Checkbox checked>High Blood</Checkbox>
      </li>
      <li>
        <Checkbox checked>Anemia</Checkbox>
      </li>
      <li>
        <Checkbox checked>Thyroid Disease</Checkbox>
      </li>
      <li>
        <Checkbox checked>Dementia</Checkbox>
      </li>
      <li>
        <Checkbox checked>Asthma</Checkbox>
      </li>
    </ul>
  );
}

export default CheckboxData;
