import React, { useContext, useState } from "react";
import { InputNumber } from "antd";
import AuthInput from "../../Inputs/AuthInput/AuthInput";
import { addFacilities } from "../FacilityContext";
import AuthAddMoreDynamicForm from "../../Inputs/AuthAddMoreDynamicForm/AuthAddMoreDynamicForm";
import "../../Modals/Modal.less";

function AuthFacilityName() {
  const addFacilityName = useContext(addFacilities);
  const medicalFacilityNameItemData = [
    {
      formItemName: "facilityName",
      formItemLabel: "Facility Name",
      formItemLayout: "11",
      dynamicContent: (
        <div>
          <AuthInput
            inputText
            inputTextName="facilityName"
            onBlur={addFacilityName}
          />
        </div>
      ),
      key: 1,
    },
    {
      formItemName: "arabicFacilityName",
      formItemLabel: "Facility Name (in Arabic)",
      formItemLayout: "11",
      dynamicContent: (
        <div>
          <AuthInput inputText inputTextName="facilityName" />
        </div>
      ),
      key: 111,
    },
    {
      formItemName: "facilityTame",
      formItemLabel: "Facility Type",
      formItemLayout: "11",
      dynamicContent: (
        <div>
          <AuthInput inputText inputTextName="facilityType" />
        </div>
      ),
      key: 11,
    },
    {
      formItemName: "branchNumber",
      formItemLabel: "Number of Branches",
      formItemLayout: "11",
      dynamicContent: <InputNumber />,
      key: 2,
    },
  ];
  return (
    <div className="auth-medical-facility">
      <AuthAddMoreDynamicForm
        backgroundStyle="bg-add-new"
        formListName="personalMedicalFacilityName"
        formItemData={medicalFacilityNameItemData}
      />
    </div>
  );
}
export default React.memo(AuthFacilityName);
