import React, { useState, useContext } from "react";
import { Row, Col, Button, Form, Input } from "antd";
import PhoneInput from "react-phone-input-2";
import ReactCodeInput from "react-verification-code-input";
import AuthLayout from "../../AuthCommon/AuthLayout";
import AuthInputs from "../MainProcessUtils/Inputs/AuthInputs";
import PolicyAndTerms from "../PolicyAndTerms/PolicyAndTermsShared/PolicyAndTermsShared";
import { ForgetPasswordForm } from "../ForgetVerifyProcess/ForgetPassword/ForgetPasswordPhone";
import "react-phone-input-2/lib/style.css";
import "../MainProcessUtils/Styling/Signup&Login.less";
import "./ForgetVerifyProcess.less";

function ForgetVerifyProcess(props) {
  const form = useContext(ForgetPasswordForm);
  const {
    onComplete,
    contentInputPassword,
    phoneValidatorMessage,
    head,
    descreption,
    button,
    buttonStatus,
    footer,
    footerlink,
    contentCode,
    contentInputEmail,
    contentInputPhone,
    extraData,
    extraDataLink,
    inputPhoneValidator,
  } = props;
  const [phone, setPhone] = useState("");
  return (
    <div className="forget-verify">
      <Row>
        <Col span="8">
          <AuthLayout />
        </Col>
        <Col span="16">
          <div className="umi-auth container">
            <div className="umi-auth-form">
              <img
                src="imgs/logo_icon.png"
                alt="umedmi"
                className="form-logo"
              />
              <h1>{props.head}</h1>
              <h3>{props.descreption}</h3>
              <div className="forget-verify-inputs">
                {props.contentCode && (
                  <div className="forget-verify-inputs-code">
                    <ReactCodeInput
                      fields={4}
                      autoFocus={true}
                      onComplete={props.onComplete}
                    />
                  </div>
                )}
                {props.contentInputEmail && (
                  <div>
                    <AuthInputs
                      inputText
                      inputTextPlaceholder="Email Address"
                      inputTextValidator=""
                      textValidatorMessage=""
                      emailIcon
                    />
                  </div>
                )}
                {props.contentInputPassword && (
                  <div>
                    <AuthInputs
                      inputPassword
                      inputPasswordPlaceholder="New Password"
                      inputPasswordName="password"
                      inputPasswordValidator=".{6}"
                      passwordValidatorMessage="at least 6 digits"
                    />
                  </div>
                )}
                {props.contentInputPhone && (
                  <div>
                    <PhoneInput
                      country={"eg"}
                      onChange={(phone) => {
                        setPhone(phone);
                        console.log(phone);
                        form.setFieldsValue({ phoneNumber: phone });
                      }}
                    />
                    <Form.Item name="phoneNumber" style={{ display: "none" }}>
                      <Input />
                    </Form.Item>
                  </div>
                )}
              </div>
              <Form.Item>
                <Button htmlType="submit" disabled={props.buttonStatus}>
                  {props.button}
                </Button>
              </Form.Item>
              <span className="verify-footer">
                {/* <Link to={props.footerlink}>{props.footer}</Link> */}
              </span>
              <span>
                {props.extraData}
                <a>{props.extraDataLink}</a>
              </span>
            </div>
            <PolicyAndTerms
              authStatus=" Don't have an account?"
              authAction=" Register."
              redirectLink="/signup"
            />
          </div>
        </Col>
      </Row>
    </div>
  );
}

export default ForgetVerifyProcess;
