import React from "react";
import { Select } from "antd";
import AuthInput from "../../Inputs/AuthInput/AuthInput";
import AuthCustomSelect from "../../Inputs/AuthCustomSelect/AuthCustomSelect";
const { Option } = Select;

const contactInfoOptions = [
  { value: "facebook", data: "FaceBook" },
  { value: "linked_in", data: "LinkedIn" },
  { value: "other", data: "Other" },
];

export const ContactInfoItemData = [
  {
    formItemName: "contactInfoType",
    formItemLabel: "Contact Info",
    formItemLayout: "7",
    key: 3,
    dynamicContent: (
      // <AuthCustomSelect options={contactInfoOptions} />
      <Select placeholder="Contact Info">
        {contactInfoOptions.map((option) => (
          <Option value={option.value} key={option.value}>
            {option.data}
          </Option>
        ))}
      </Select>
    ),
  },
  {
    formItemName: "link",
    formItemLabel: "contact info link",
    formItemLayout: "16",
    key: 4,
    dynamicContent: (
      <div>
        <AuthInput inputText inputTextName="link" />
      </div>
    ),
  },
];
