import React, { useState, useContext } from 'react';
import AuthInput from '../../Inputs/AuthInput/AuthInput';
import AuthInputSelect from '../../Inputs/AuthInputSelect';
import AuthInputUpload from '../../Inputs/AuthUpload/AuthInputUpload';
import AuthInputRadio from '../../Inputs/AuthInputRadio/AuthInputRadio';
import { DatePicker } from 'antd';

import { facilityName, facilities } from '../../SecondStep/FacilityContext';

const ValidityData = [
  { value: '1', data: 'Temporary' },
  { value: '2', data: 'Permanent' },
];
export function LicensureMedicalFacilityItemData() {
  const facilitiesNames: facilityName[] = useContext(facilities);

  return [
    {
      formItemName: '',
      formItemLayout: '24',
      dynamicContent: <AuthInputUpload />,
      key: 1,
    },

    {
      formItemName: 'facilityName',
      formItemLabel: 'Facility Name',
      formItemLayout: '11',
      dynamicContent: <AuthInputSelect options={facilitiesNames} inputSelect />,
      key: 2,
    },
    {
      formItemName: 'BranchName',
      formItemLabel: 'Branch Name',
      formItemLayout: '11',

      dynamicContent: <AuthInputSelect inputSelect />,
      key: 2,
    },
    {
      formItemName: 'documentName',
      formItemLayout: '24',
      dynamicContent: (
        <div>
          <AuthInput inputText inputTextName='documentName' inputTextLabel='Document Name' />
        </div>
      ),
      key: 7,
    },
    {
      formItemName: 'organization',
      formItemLayout: '24',
      dynamicContent: (
        <div>
          {' '}
          <AuthInput inputText inputTextName='organization' inputTextLabel='Issuing Organization' />
        </div>
      ),
      key: 8,
    },
    {
      formItemName: 'test1',
      formItemLayout: '11',
      dynamicContent: <AuthInputSelect inputSelect inputSelectLabel='Type' />,
      key: 9,
    },
    {
      formItemName: 'test1',
      formItemLayout: '11',
      dynamicContent: <AuthInputRadio inputRadio radioOptions={ValidityData} radioLabel='Validity' />,
      key: 10,
    },
    {
      formItemName: 'issueDate',
      formItemLayout: '11',
      dynamicContent: <DatePicker placeholder='Issue Date' />,
      key: 11,
    },
    {
      formItemName: 'expiryDate',
      formItemLayout: '11',
      dynamicContent: <DatePicker placeholder='Expiry Date' />,
      key: 12,
    },
  ];
}
