import React from "react";
import { Form, Input } from "antd";
import { UserOutlined, LockOutlined, MailOutlined } from "@ant-design/icons";

import { Iprops } from "./AuthInputsTypes";

function AuthInputs({
  inputText,
  inputTextName,
  inputTextPlaceholder,
  inputTextValidator,
  textValidatorMessage,
  inputPassword,
  inputPasswordPlaceholder,
  inputPasswordName,
  inputPasswordValidator,
  passwordValidatorMessage,
  userIcon,
  emailIcon,
}: Iprops) {
  return (
    <div>
      {inputText && (
        <Form.Item
          name={inputTextName}
          rules={[
            {
              validator(rule, value) {
                const reg = new RegExp(`${inputTextValidator}`);
                if (reg.test(value)) {
                  return Promise.resolve();
                }
                return Promise.reject(`${textValidatorMessage}`);
              },
            },
          ]}
          hasFeedback
        >
          <Input
            prefix={
              userIcon ? <UserOutlined /> : emailIcon ? <MailOutlined /> : null
            }
            placeholder={inputTextPlaceholder}
          />
        </Form.Item>
      )}

      {inputPassword && (
        <Form.Item
          name={inputPasswordName}
          shouldUpdate
          rules={[
            {
              validator(rule, value) {
                const reg = new RegExp(`${inputPasswordValidator}`);
                if (reg.test(value)) {
                  return Promise.resolve();
                }
                return Promise.reject(`${passwordValidatorMessage}`);
              },
            },
          ]}
          hasFeedback
        >
          <Input.Password
            prefix={<LockOutlined />}
            placeholder={inputPasswordPlaceholder}
          />
        </Form.Item>
      )}
    </div>
  );
}

export default AuthInputs;
