import React, { useState } from "react";
import { Upload, Modal } from "antd";
import { PlusOutlined } from "@ant-design/icons";
import "./AuthInputUpload.less";

function getBase64(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result);
    reader.onerror = (error) => reject(error);
  });
}

function AuthInputUpload() {
  const [uploaded, setUploaded] = useState([{}]);
  const [previewVisible, setPreviewVisible] = useState(false);
  const [previewImage, setPreviewImage] = useState("");
  console.log(uploaded);

  const handleCancel = () => setPreviewVisible(false);
  const handlePreview = async (file) => {
    if (!file.url && !file.preview) {
      file.preview = await getBase64(file.originFileObj);
    }

    setPreviewImage(file.url || file.preview);
    setPreviewVisible(true);
  };

  const handleChange = (fileList) => setUploaded([fileList.fileList]);
  // const handleChange = (fileList) => console.log("onChange", fileList.fileList);

  return (
    <div className="auth-input-upload">
      <Upload
        listType="picture-card"
        onPreview={handlePreview}
        onChange={handleChange}
      >
        {uploaded.length >= 8 ? null : (
          <div>
            <PlusOutlined />
            <p> Upload</p>
          </div>
        )}
      </Upload>
      <Modal
        visible={previewVisible}
        footer={null}
        onCancel={handleCancel}
        className="custom-modal"
      >
        <img alt="upload" src={previewImage} />
      </Modal>
    </div>
  );
}

export default AuthInputUpload;
