import React, { useContext, useEffect } from "react";
import AuthAddMoreDynamicForm from "../Inputs/AuthAddMoreDynamicForm/AuthAddMoreDynamicForm";
import { PersonnelLicensureFormItemData } from "./FourthStepData/PersonnelLicensureData";
import { FormContext } from "../AuthProfileComplete";

function AuthPersonnelLicensure() {
  const personnelLicensureFormItemData = PersonnelLicensureFormItemData;

  const form = useContext(FormContext);

  return (
    <div>
      <div className="personal-licensure-section" style={{ marginTop: "20px" }}>
        <AuthAddMoreDynamicForm
          formItemData={personnelLicensureFormItemData}
          formListName="licensure"
          backgroundStyle="bg-add-new"
        />
      </div>
    </div>
  );
}

export default AuthPersonnelLicensure;
