import React, { useState, useEffect, createContext } from "react";
import axios from "axios";
import { useHistory } from "react-router-dom";
import { Form, Row, Col, message } from "antd";
import AuthLayout from "../../../AuthCommon/AuthLayout";
import AuthSocialMedia from "../../AuthSocialMedia/AuthSocialMedia";
import PolicyAndTerms from "../../PolicyAndTerms/PolicyAndTermsShared/PolicyAndTermsShared";

import "../../MainProcessUtils/Styling/Signup&Login.less";
import SignupOptionsForm from "./SignupOptionsForm";
import { signupErrors } from "./SignupErrors";

export const SignupFormContext = createContext({} as any);

function SignupOptions() {
  const [form] = Form.useForm();
  const history = useHistory();
  const [, forceUpdate] = useState();

  useEffect(() => {
    forceUpdate({});
  }, []);

  const serviceProviderRegistration = (signupData) => {
    console.log(signupData);
    localStorage.setItem(
      "registrationPhoneNumber",
      form.getFieldValue("phoneNumber")
    );
    axios({
      method: "POST",
      url: "/u-consultation/auth/register",
      data: signupData,
    })
      .then((response) => {
        console.log(response);
        history.push("/verify-registration");
      })
      .catch((error) => {
        if (error.response) {
          console.log(error.response.data.message);
          const errorMessage = error.response.data.message;
          signupErrors(errorMessage, form);
        } else {
          message.error("An error occurred. Please try again.");
        }
      });
  };

  return (
    <div>
      <Row>
        <Col span="8">
          <AuthLayout />
        </Col>
        <SignupFormContext.Provider value={form}>
          <Col span="16">
            <div className="umi-auth container">
              <div className="umi-auth-form ">
                <img
                  src="imgs/logo_icon.png"
                  alt="umedmi"
                  className="form-logo"
                />
                <h1>Register</h1>

                <Form
                  form={form}
                  onFinish={serviceProviderRegistration}
                  initialValues={{ businessType: "patient" }}
                >
                  <SignupOptionsForm />
                </Form>
                <div className="auth-social">
                  <AuthSocialMedia title="Register With" />
                </div>
              </div>
            </div>
            <PolicyAndTerms
              authAction="Sign in."
              authStatus=" Already Registered?"
              redirectLink="/login"
            />
          </Col>
        </SignupFormContext.Provider>
      </Row>
    </div>
  );
}
export default SignupOptions;

