import React, { useState } from 'react';
import Fullscreen from 'react-full-screen';
import Reservation from './Reservation/Reservation';
import FirstPatient from './FirstPatient/FirstPatient';
import CountdownTime from '../Common/CountdownTime/CountdownTime';

import { Row, Col } from 'antd';

import './ClinicViewStart.less';

function ClinicViewStart() {
  const [isFull, setFullScreen] = useState(false);

  return (
    <Fullscreen enabled={isFull} onChange={(isFull) => setFullScreen(isFull)}>
      <div className='umi-clinic-start-content'>
        <div className='umi-container'>
          <Row gutter={[{ lg: 25 }, 10]}>
            <Col md={24} lg={14}>
              <FirstPatient />
            </Col>
            <Col md={24} lg={10}>
              <CountdownTime time={16000000} title='Time remaining for leaving' />
              <Reservation />
            </Col>
          </Row>
        </div>
      </div>
    </Fullscreen>
  );
}

export default ClinicViewStart;
