import React from 'react';
import { Form, Input } from 'antd';

type IProps = {
  name?: string;
  placeholder?: string;
  type?: string;
  handleChange?: any;
};

function UmiInputs({ type, name, placeholder, handleChange }: IProps) {
  return (
    <Form.Item name={name} rules={[{ required: false, message: `Please input your ${placeholder}!` }]}>
      <Input min={1} onChange={handleChange} type={type} placeholder={placeholder} className="hello" />
    </Form.Item>
  );
}

export default UmiInputs;
