import React from 'react';
import { Form, Input } from 'antd';
// import Handwriting from './Handwriting';
import SpeechRecognation from './SpeechRecognation';

type IProps = {
  name: string | undefined;
  placeholder: string | undefined;
};

function UmiTextarea({ name, placeholder }: IProps) {
  return (
    <Form.Item className="umi-textarea-icons">
      <div className="umi-icons-wrapper">
        {/* <Handwriting /> */}
        <img src="icons/writing.svg" alt="Write Image" className="handwriting" />
        <SpeechRecognation inputName={name} />
      </div>
      <Form.Item name={name}>
        <Input.TextArea rows={4} placeholder={placeholder} />
      </Form.Item>
    </Form.Item>
  );
}

export default UmiTextarea;
