import React, { useContext } from 'react';
import { PlusCircleOutlined } from '@ant-design/icons';
import { FormContext } from '../../PatientDetalis';

import './AddMedicine.less';

type IProps = {
  parentCallback: any;
  disabled?: boolean;
  medicine?: boolean;
  lab?: boolean;
  radio?: boolean;
  consum?: boolean;
};

function AddMedicine({ parentCallback, disabled, medicine, lab, radio, consum }: IProps) {
  const form = useContext(FormContext);
  const { getFieldsValue, resetFields } = form;

  const getValues = () => {
    {
      medicine &&
        parentCallback(
          getFieldsValue([
            'prescription-medicine',
            'medicine-capsules',
            'medicine-times',
            'medicine-weeks',
            'medicine-notes',
          ])
        );
      resetFields(['prescription-medicine', 'medicine-capsules', 'medicine-times', 'medicine-weeks', 'medicine-notes']);
    }
    {
      lab && parentCallback(getFieldsValue(['prescription-laboratory', 'laboratory-notes']));
      resetFields(['prescription-laboratory', 'laboratory-notes']);
    }
    {
      radio &&
        parentCallback(
          getFieldsValue([
            'prescription-radiology',
            'radiology-investigation',
            'organ-scan',
            'radiology-tincture',
            'radiology-anesthesia',
            'radiology-labs',
            'radiology-notes',
          ])
        );
      resetFields([
        'prescription-radiology',
        'radiology-investigation',
        'organ-scan',
        'radiology-tincture',
        'radiology-anesthesia',
        'radiology-labs',
        'radiology-notes',
      ]);
    }
    {
      consum && parentCallback(getFieldsValue(['prescription-consumable', 'consumable-notes']));
      resetFields(['prescription-consumable', 'consumable-notes']);
    }
  };

  return (
    <button className='umi-prescription-add' disabled={disabled} onClick={getValues}>
      <PlusCircleOutlined />
    </button>
  );
}

export default AddMedicine;
