import React, { useEffect, useState } from "react";
import { message } from "antd";
import axios from "axios";
import { useHistory } from "react-router-dom";
import Countdown from "react-countdown";

import ForgetVerifyProcess from "../ForgetVerifyProcess/ForgetVerifyProcess";

function VerifyRegistration() {
  const history = useHistory();
  const buttonStatus = true;
  const [timerStatus, setTimerStatus] = useState(true);

  function resendOtpService() {
    axios({
      method: "POST",
      url: "/u-consultation/auth/resend-phone-otp",
      data: localStorage.getItem("registrationPhoneNumber"),
    })
      .then((response) => {
        console.log(response);
      })
      .catch((error) => {
        console.log(error);
        if (error.response) {
          console.log(error.response.data.message);
        } else {
          message.error("An error occurred. Please try again.");
        }
      });
  }
  useEffect(() => {
    resendOtpService();
  }, []);

  const handleRevrefiy = () => {
    resendOtpService();
  };
  const handleConfirmRegistration = (code) => {
    const otpData = {
      type: "OTP",
      username: localStorage.getItem("registrationPhoneNumber"),
      password: code,
    };
    axios({
      method: "POST",
      url: "u-consultation/auth/login",
      data: otpData,
    })
      .then((response) => {
        console.log(response);
        history.push("/login");
      })
      .catch((error) => {
        console.log(error.response.data.message);
        if (error.response.data.message === "Invalid Otp") {
          message.error("Invalid Code");
        }
      });
  };

  const renderer = ({ hours, minutes, seconds, completed }) => {
    if (completed) {
      setTimerStatus(false);
      return <div></div>;
    } else {
      return (
        <span>
          {hours}:{minutes}:{seconds}
        </span>
      );
    }
  };
  return (
    <div className="verify">
      <ForgetVerifyProcess
        head="Register"
        descreption="Please enter the verification code sent to your mobile."
        button="Verify"
        buttonStatus={buttonStatus}
        contentCode
        onComplete={handleConfirmRegistration}
        extraDataLink={
          <span>
            {timerStatus === true ? (
              <div>
                <span>Resend in </span>
                <Countdown date={Date.now() + 1000 * 90} renderer={renderer} />
              </div>
            ) : (
              <div
                onClick={() => {
                  setTimerStatus(true);
                  handleRevrefiy();
                }}
              >
                click here to resend
              </div>
            )}

            {/* <span> minutes </span> */}
          </span>
        }
      />
    </div>
  );
}
export default VerifyRegistration;
