import React from "react";
import { Input, Form, DatePicker, Checkbox, InputNumber } from "antd";
import AuthPhoneInput from "../AuthPhoneInput";
import { Iprops } from "./InputTypes";
import "./AuthInput.less";

function AuthInput({
  inputText,
  inputTextName,
  inputTextLabel,
  inputTextPlaceholder,
  inputEmail,
  inputEmailName,
  inputEmailLabel,
  inputDate,
  DateName,
  inputDateLabel,
  inputPhone,
  inputPhoneValidator,
  phoneValidatorMessage,
  textArea,
  textAreaName,
  textAreaLabel,
  textAreaPlaceholder,
  checkBox,
  checkBoxData,
  checkStatus,
  disableStatus,
  inputNumber,
  onClick,
  onBlur,
  onChange,
  inputValue,
}: Iprops) {
  return (
    <div className="auth-input">
      {inputText && (
        <Form.Item name={inputTextName} label={inputTextLabel}>
          <Input
            placeholder={inputTextPlaceholder}
            onClick={onClick}
            onBlur={onBlur}
            onChange={onChange}
            value={inputValue}
          />
        </Form.Item>
      )}

      {inputEmail && (
        <Form.Item name={inputEmailName} label={inputEmailLabel}>
          <Input />
        </Form.Item>
      )}
      {inputDate && (
        <Form.Item label={inputDateLabel} name={DateName}>
          <DatePicker />
        </Form.Item>
      )}

      {inputPhone && (
        <AuthPhoneInput
          inputPhoneValidator={inputPhoneValidator}
          phoneValidatorMessage={phoneValidatorMessage}
        />
      )}
      {textArea && (
        <Form.Item name={textAreaName} label={textAreaLabel}>
          <Input.TextArea placeholder={textAreaPlaceholder} />
        </Form.Item>
      )}

      {checkBox && (
        <div>
          <Form.Item>
            {disableStatus ? (
              <Checkbox defaultChecked={checkStatus} disabled>
                {checkBoxData}
              </Checkbox>
            ) : (
              <Checkbox defaultChecked={checkStatus}>{checkBoxData}</Checkbox>
            )}
          </Form.Item>
        </div>
      )}
      {inputNumber && (
        <div>
          <InputNumber />
        </div>
      )}
    </div>
  );
}

export default AuthInput;
