import React from "react";
import { Steps } from "antd";
import "./AuthSuccess.less";
import SubscriptionPlan from "./SubscriptionPlan/SubscriptionPlan";
import UnderReview from "./underReview/UnderReview";
import AuthAsideProgress from "../AuthCompleteProcess/Progress/AuthAsideProgress";
import AuthAds from "../AuthCommon/AuthAds";

const { Step } = Steps;

function AuthSucess() {
  return (
    <div>
      <div className="auth-success">
        <div className="auth-success-content container">
          <AuthAsideProgress profilePercentage="50" />
          <div className="auth-success-content-steps">
            <Steps current={1}>
              <Step title="1" description="Account Created" />
              <Step title="2" description="Subscription Plans" />
              <Step title="3" description="Under Review" />
              <Step title="4" description="Approved" />
            </Steps>
          </div>
          {/* <UnderReview /> */}
          <SubscriptionPlan />
          <AuthAds />
        </div>
      </div>
    </div>
  );
}

export default AuthSucess;
