import React, { useState } from "react";
import { Input, Select, InputNumber } from "antd";
import PhoneInput from "react-phone-input-2";
import AuthAddMoreDynamicForm from "../../../Inputs/AuthAddMoreDynamicForm/AuthAddMoreDynamicForm";
import "react-phone-input-2/lib/style.css";
import "./AuthPersonallInfoPhones.less";

const { Option } = Select;

function AuthPersonalInfoPhones() {
  const items = ["Main", "Emergency", "Personal", "Clinic"];
  const [personalPhones, setPersonalPhones] = useState([] as any);
  console.log(personalPhones);
  const personnallPhoneItemData = [
    {
      formItemName: "phoneType",
      formItemLabel: "Phone Type",
      formItemLayout: "8",
      dynamicContent: (
        <Select placeholder="Phone Type">
          {items.map((item) => (
            <Option key={item} value={item}>
              {item}
            </Option>
          ))}
        </Select>
      ),
      key: 3,
    },
    {
      formItemName: "phoneNumber",
      formItemLabel: "Phone Number",
      formItemLayout: "15",
      dynamicContent: (
        <div>
          <PhoneInput
            country={"eg"}
            // onBlur={(phone) => {
            //   setPersonalPhones([...personalPhones, phone]);
            //   console.log(personalPhones);
            // }}
            onBlur={(e) => {
              debugger;
              if (e.target.value.length > 4) {
                personalPhones.map((phone) => {
                  setPersonalPhones([...personalPhones, e.target.value]);
                });
                // setPersonalPhones([...personalPhones, e.target.value]);
              }
            }}
          />
        </div>
      ),
      key: 1,
    },
  ];

  return (
    <div className="auth-personal-info-phone">
      <AuthAddMoreDynamicForm
        formListName="phones"
        formItemData={personnallPhoneItemData}
        backgroundStyle="bg-add-new"
      />
    </div>
  );
}

export default AuthPersonalInfoPhones;
