import React from 'react';
import { BrowserRouter as Router, Route, Redirect, Switch } from 'react-router-dom';
import AuthSuccess from '../Components/Auth/Success/AuthSuccess';
import AuthProfileComplete from '../Components/Auth/AuthCompleteProcess/AuthProfileComplete';
import MainInvitation from '../Components/Auth/Success/SubscriptionPlan/Invitation/MainInvitation/MainInvitation';

function CompleteSignupRoutes() {
  return (
    <div>
      <Router>
        <Switch>
          <Route path='/completeSignup' component={AuthProfileComplete} />
          <Route path='/auth-success' component={AuthSuccess} />
          <Route path='/invite-colleagues' component={MainInvitation} />
          <Redirect to='/completeSignup' />
        </Switch>
      </Router>
    </div>
  );
}

export default CompleteSignupRoutes;
