import React from "react";
import { Progress, Form, Affix } from "antd";
import "./MainInvitationContent.less";
import AuthInput from "../../../../../AuthCompleteProcess/Inputs/AuthInput/AuthInput";
import AuthAds from "../../../../../AuthCommon/AuthAds";

function MainInvitationContent() {
  const initialValues = { inviteColleagues: [{}] };

  return (
    <div className="main-invitation-content container">
      <Progress
        type="circle"
        strokeColor={{
          "0%": " #c398c9",
          "50%": "#ff598b",
        }}
        percent={20}
      />
      <div className="main-invitation-content-description">
        <h2>Invite Colleagues</h2>
        <p>
          Please fill the following fields with your colleague's info. Once at
          lease 10 of your colleagues register, you will redeem your free gold
          month.
        </p>
      </div>
      <div>
        <Form name="invite" initialValues={initialValues}>
          <AuthInput
            inputText
            inputTextName="name"
            inputTextPlaceholder="Name Here"
          />
        </Form>
      </div>
      <div>
        <Affix
          //   offsetBottom={1}
          style={{ position: "absolute", bottom: "1px", left: "0" }}
        >
          <p>ss</p>
        </Affix>
      </div>
    </div>
  );
}

export default MainInvitationContent;
