import * as React from 'react';
import { Link } from 'react-router-dom';
import { FormattedMessage } from 'react-intl';
import { Avatar, Row, Col, Tooltip } from 'antd';
import { QrcodeOutlined, ArrowUpOutlined } from '@ant-design/icons';
import Status from '../../Common/Status/Status';
import ClinicTitle from '../ClinicTitle/ClinicTitle';

import './FirstPatient.less';

function FirstPatient() {
  const show = true;

  return (
    <>
      <Row gutter={12}>
        <Col sm={16}>
          <div className="umi-next-patient">
            <ClinicTitle title="Next Patient" />
            <div className="umi-patient-wrapper">
              <div className="umi-patient-data">
                <Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />
                <div className="umi-patient-info">
                  <div className="umi-patient-name">Travis Mueller</div>
                  <div className="umi-patient-id-status">
                    <div className="umi-patient-id">#25971</div>
                    <div className="umi-patient-status">
                      <Status title="Critical" />
                    </div>
                  </div>
                </div>
              </div>
              <div className="umi-icons">
                <QrcodeOutlined />
                <Tooltip placement="top" title="Edit E-Prescription">
                  <div className="umi-edit-button-wrapper">
                    <img src="icons/edit-button.svg" alt="Edit Button" />
                  </div>
                </Tooltip>
              </div>
            </div>
          </div>
        </Col>
        <Col sm={8}>
          <div className="umi-paused-patients">
            <ClinicTitle title="Paused Patients" count={10} />
            <div className="umi-paused-patients-wrapper">
              <div className="umi-critical-fast">3 Critical . 7 Fast</div>
              <div className="umi-paused-patinets-avatars">
                <div className="umi-avatars">
                  <Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />
                  <Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />
                  <div className="umi-avatar-wrapper">+8</div>
                </div>
                <Tooltip placement="top" title="View All">
                  <div className="umi-view-all-wrapper">
                    <ArrowUpOutlined rotate={40} />
                  </div>
                </Tooltip>
              </div>
            </div>
          </div>
        </Col>
      </Row>

      <div className="umi-start-patient">
        <div className="umi-start-patient-header">
          <h4>
            <FormattedMessage id="clinic.view" defaultMessage="Clinic View" />
          </h4>
          <div className="umi-ready">Ready for the next patient</div>
        </div>
        <figure>
          <div className="umi-img-wrapper">
            <img src="icons/illustration.svg" alt="Illustration Img" />
          </div>
          <p className="umi-press">Press Start button to receive patients</p>
          <Link to="/patient-details">
            <button className="umi-btn"> Start</button>
          </Link>
        </figure>
      </div>
    </>
  );
}

export default FirstPatient;
