import React, { useContext } from "react";
import { Button } from "antd";
import { LinkedIn } from "react-linkedin-login-oauth2";

// import { getLinkedInData } from "../../../../Services/AuthService";

import "./AuthSocialMedia.less";
import { SignupFormContext } from "../AuthSignup/SignupOptions/SignupOptions";
import { LoginFormContext } from "../AuthLogin/Login";

function AuthLinkedInAccount(props) {
  const signupForm = useContext(SignupFormContext);
  const loginForm = useContext(LoginFormContext);

  const successResponse = (response) => {
    // Sign Up Option
    if (Object.keys(signupForm).length !== 0) {
      // signupForm.setFieldsValue({ 'userName': response.name });
      // signupForm.setFieldsValue({ 'email': response.email });
      // signupForm.setFieldsValue({ 'token': response });
      console.log(response);
      // getLinkedInData(response.code, "http://localhost:3000/signup-options");
    }
    // Login Option
    if (Object.keys(loginForm).length !== 0) {
      // loginForm.setFieldsValue({ 'userID': response.name });
      // loginForm.setFieldsValue({ 'token': response });
      console.log(response);
    }
  };

  const failureResponse = (error) => {
    console.log(error);
  };
  return (
    <div className="auth-linkedin">
      {/* <Button type="link" onClick={onClick} disabled={disabled} icon={<img src="/imgs/icons/auth/socialMedia/linkedin.png" alt='Linked In' style={{ width: '24px', height: '24px' }} />} size='small'></Button> */}
      <LinkedIn
        clientId="86775k4abkq9w0"
        onFailure={failureResponse}
        onSuccess={successResponse}
        redirectUri="http://localhost:3000/linkedin"
        scope="r_liteprofile r_emailaddress"
        renderElement={({ onClick, disabled }) => (
          <Button
            type="link"
            onClick={onClick}
            disabled={disabled}
            icon={
              <img
                src="/imgs/icons/auth/socialMedia/linkedin.png"
                alt="Linked In"
              />
            }
            size="small"
          ></Button>
        )}
      />
    </div>
  );
}
export default AuthLinkedInAccount;
