import React, { useState, createContext } from "react";
import { Row, Col, Affix, Form, message } from "antd";
import axios from "axios";
import AuthLayout from "../AuthCommon/AuthLayout";
import { allSteps } from "./AuthUtils/AuthStepsConstants";
import AuthMainSteps from "./AuthMainActionAndSteps/AuthMainSteps";
import { allInitialValues } from "./AuthUtils/AuthInitialValues";
import AuthProfileCompleteActions from "./AuthMainActionAndSteps/AuthProfileCompleteActions";
import "./AuthProfileComplete.less";

export const FormContext = createContext({} as any);
export const TabContext = createContext("");
function AuthProfileComplete() {
  const steps = allSteps;
  const initialValues = allInitialValues;
  const [tabs, setTabs] = useState("1");
  const [current, setCurrent] = useState(0);
  const [licensure, setLicensure] = useState([{}]);
  const [form] = Form.useForm();

  console.log(form.getFieldsValue());
  const next = () => {
    if (current == 1 || current == 3) {
      if (tabs == "1") {
        setTabs("2");
      } else {
        setTabs("1");
        setCurrent(current + 1);
      }
    } else {
      setTabs("1");
      setCurrent(current + 1);
    }
    //api

    axios
      .post("/u-consultation/complete-profile", form.getFieldsValue())
      .then((response) => {
        if (response.data.data) {
          console.log(response.data.data);
        }
        return response.data;
      })
      .catch((error) => {
        console.log(error);
        if (error.response) {
          console.log(error.response.data.message);
        } else {
          message.error("An error occurred. Please try again.");
        }
      });
  };

  const prev = () => {
    if (current == 1 || current == 3) {
      if (tabs == "2") {
        setTabs("1");
      } else {
        setTabs("2");
        setCurrent(current - 1);
      }
    } else {
      setTabs("2");
      setCurrent(current - 1);
    }
  };
  const validateMessages = {
    required: "${label} is required!",
    types: {
      email: "must be a Valid Email",
      number: "must be a Number",
    },
  };
  return (
    <div>
      <Row>
        <Col span={8}>
          <AuthLayout
            accountPercentage={(current + 1) * 20}
            profilePercentage={(current + 1) * 10}
          />
        </Col>
        <Col span={16}>
          <div className="auth-profile-complete">
            <div className="container">
              <TabContext.Provider value={tabs}>
                <FormContext.Provider value={form}>
                  <Form
                    form={form}
                    initialValues={initialValues}
                    onFinish={(values) => console.log(values)}
                    validateMessages={validateMessages}
                  >
                    <AuthMainSteps steps={steps} current={current} />
                    <Affix offsetBottom={1}>
                      <AuthProfileCompleteActions
                        current={current}
                        steps={steps}
                        prev={prev}
                        next={next}
                      />
                    </Affix>
                  </Form>
                </FormContext.Provider>
              </TabContext.Provider>
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
}

export default AuthProfileComplete;
