import React from 'react';
import PatientReservation from '../../Common/PatientReservation/PatientReservation';
import { Select, Tabs } from 'antd';

import './Reservation.less';

const { Option } = Select,
  { TabPane } = Tabs;

const reservationSelect = (
  <Select defaultValue="Sort by reservation" style={{ width: '100%' }}>
    <Option value="critical-cases">Critical Cases</Option>
    <Option value="quick-reservation">Quick Reservation</Option>
    <Option value="reservation-descending">Reservation Descending</Option>
    <Option value="reservation-ascending">Reservation Ascending</Option>
  </Select>
);

const patientsReservation = (
  <div className="umi-patients-reservation">
    <PatientReservation quick play />
    <PatientReservation critical play />
    <PatientReservation play />
    <PatientReservation quick play />
    <PatientReservation critical play />
    <PatientReservation play />
  </div>
);

const exaimedReservation = (
  <div className="umi-patients-reservation">
    <PatientReservation quick examined />
    <PatientReservation critical examined />
    <PatientReservation examined />
    <PatientReservation quick examined />
    <PatientReservation critical examined />
    <PatientReservation examined />
  </div>
);

function Reservation() {
  return (
    <div className="umi-resarvation-wrapper">
      <div className="umi-patients-type">
        <Tabs defaultActiveKey="patients">
          <TabPane tab="Patients" key="patients">
            <div className="umi-sort-reservation">
              {reservationSelect}
              {patientsReservation}
            </div>
          </TabPane>
          <TabPane tab="Examined" key="examined">
            <div className="umi-sort-reservation">
              {reservationSelect}
              {exaimedReservation}
            </div>
          </TabPane>
        </Tabs>
      </div>
    </div>
  );
}

export default Reservation;
