import React, { useState, useEffect, useContext } from "react";
import { Form, Button, Input } from "antd";
import { Link } from "react-router-dom";
import Captcha from "./AuthCaptcha";
import { LoginFormContext } from "./Login";
import AuthInputs from "../MainProcessUtils/Inputs/AuthInputs";
import "../MainProcessUtils/Styling/Signup&Login.less";

function LoginForm() {
  const [, forceUpdate] = useState();
  const loginForm = useContext(LoginFormContext);

  useEffect(() => {
    forceUpdate({});
  }, []);

  return (
    <div>
      <Form.Item name="type" style={{ display: "none" }}>
        <Input />
      </Form.Item>
      <AuthInputs
        inputText
        inputTextName="username"
        inputTextPlaceholder="User name  or Phone number"
        inputTextValidator="^[A-Za-z0-9_]+$"
        textValidatorMessage="empty !!"
      />
      <AuthInputs
        inputPassword
        inputPasswordName="password"
        inputPasswordPlaceholder="Password"
        inputPasswordValidator=".{4}"
        passwordValidatorMessage="at least 4 digits"
        userIcon
      />
      <Captcha />
      <Form.Item name="captcha" style={{ display: "none" }}>
        <Input />
      </Form.Item>

      <Form.Item shouldUpdate={true}>
        {() => (
          <Button
            htmlType="submit"
            // @ts-ignore
            disabled={
              !loginForm.isFieldTouched("username") ||
              !loginForm.isFieldTouched("password") ||
              !loginForm.getFieldValue("captcha") ||
              loginForm.getFieldsError().filter(({ errors }) => errors.length)
                .length
            }
          >
            sign in
          </Button>
        )}
      </Form.Item>

      <span className="forget-password">
        <Link to="/forget-password-phone">Forget Password ?</Link>
      </span>
    </div>
  );
}

export default LoginForm;
