import React from "react";
import { Form, Row, Col, Button } from "antd";
import { PlusOutlined, DeleteOutlined } from "@ant-design/icons";
import "./AuthAddMoreDynamicForm.less";

type Iprops = {
  backgroundStyle?: string;
  addFieldStyle?: any;
  formItemData?: any;
  formListName?: any;
};

function AuthAddMoreDynamicForm({
  //formItemData=[{ formItemName: "test1",formItemLayout:"11",dynamicContent:<div></div>,key:1,formItemRules}]
  formItemData,
  formListName,
  backgroundStyle,
}: Iprops) {
  return (
    <div className="auth-add-more ">
      <Form.List name={formListName}>
        {(fields, { add, remove }) => {
          return (
            <div>
              {fields.map((field, index) => (
                <div key={field.key}>
                  <Row justify="space-between" className={backgroundStyle}>
                    {formItemData.map((data) => (
                      <React.Fragment key={data.key}>
                        <Col span={data.formItemLayout}>
                          <Form.Item
                            label={data.formItemLabel}
                            style={{ marginBottom: "unset" }}
                            key={data.formItemName}
                            name={[index, `${data.formItemName}`]}
                            rules={data.formItemRules}
                          >
                            {data.dynamicContent}
                          </Form.Item>
                        </Col>
                      </React.Fragment>
                    ))}
                  </Row>
                  {fields.length > 1 ? (
                    <span onClick={() => remove(field.name)}>
                      <DeleteOutlined />
                    </span>
                  ) : null}
                </div>
              ))}

              <Form.Item>
                <span onClick={() => add()} className="add-field-container">
                  <PlusOutlined />
                </span>
              </Form.Item>
            </div>
          );
        }}
      </Form.List>
    </div>
  );
}

export default AuthAddMoreDynamicForm;
