import React, { useState, Fragment, useContext, useEffect } from "react";
import axios from "axios";
import { Form, Input, Spin } from "antd";
import { HeartOutlined } from "@ant-design/icons";
// import InfiniteScroll from 'react-infinite-scroll-component';
import { FormContext } from "../../../PatientDetails/PatientDetalis";

import "./SearchInput.less";

type IProps = {
  name: string;
  placeholder: string;
  handleChange?: any;
};

function SearchInput({ name, placeholder, handleChange }: IProps) {
  const [value, setValue] = useState("");
  const [data, setData] = useState(Array.from({ length: 20 }) as any);
  const [showList, setShowList] = useState(false);
  const [favorite, setFavorite] = useState(false);
  const [loading, setLoading] = useState(true);
  const form = useContext(FormContext);

  useEffect(() => {
    axios
      .get("https://jsonplaceholder.typicode.com/todos")
      .then((res) => {
        setData(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const generateShowList = () => {
    setShowList(true);
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
    }, 2000);
  };

  const displayValue = (e) => {
    form.setFieldsValue({ [name]: e.target.textContent });
    setShowList(false);
    handleChange();
  };

  const generateFavorite = (index: number) => {
    setShowList(true);
    data[index].completed = !data[index].completed;
    setFavorite(!favorite);
  };

  const fetchMoreData = () => {
    setTimeout(() => {
      setData(data.concat(Array.from({ length: 20 })));
    }, 1500);
  };

  const listItem = (item, index) => (
    <li className="search-iput-item">
      <div className="list-title" onClick={displayValue}>
        {item.title}
      </div>
      <div
        className={`favorite-item ${item.completed && "active"}`}
        onClick={() => generateFavorite(index)}
      >
        <HeartOutlined />
      </div>
    </li>
  );

  return (
    <div className="search-input-wrapper">
      <Form.Item
        name={name}
        rules={[{ required: false, message: "Please input your Subject!" }]}
      >
        <Input
          placeholder={placeholder}
          autoComplete="off"
          value={value}
          onChange={(e) => {
            setValue(e.target.value);
          }}
          onFocus={generateShowList}
        />
      </Form.Item>
      {showList && (
        <>
          <div
            className="list-background"
            onClick={() => {
              setShowList(false);
            }}
          />
          <ul className="search-input-list">
            <Spin spinning={loading} tip="Loading...">
              {/* <InfiniteScroll
                dataLength={data.length}
                next={fetchMoreData}
                hasMore={true}
                loader={<h4>Loading...</h4>}
              > */}
              {data.map((item, index) => (
                <Fragment key={item.id}>
                  {value.length !== 0 ? (
                    <>
                      {item.title.toLowerCase().includes(value.toLowerCase()) &&
                        listItem(item, index)}
                    </>
                  ) : (
                    listItem(item, index)
                  )}
                </Fragment>
              ))}
              {/* </InfiniteScroll> */}
            </Spin>
          </ul>
        </>
      )}
    </div>
  );
}

export default SearchInput;
