import React from "react";
import { Progress } from "antd";
import "./AuthAsideProgress.less";
type Iprops = {
  profilePercentage?: any;
};
function AuthAsideProgress({ profilePercentage }: Iprops) {
  return (
    <div className="auth-aside-progress">
      <Progress
        type="circle"
        strokeColor={{
          "0%": " #c398c9",
          "50%": "#ff598b",
        }}
        percent={profilePercentage}
        format={(percent) => (
          <span>
            <span style={{ display: "block", marginBottom: "6px" }}>
              {percent}%
            </span>
            Profile <br></br>Progress
          </span>
        )}
      />
    </div>
  );
}

export default AuthAsideProgress;
