import React, { createContext } from "react";
import { useHistory } from "react-router-dom";
import { Form, message } from "antd";
import axios from "axios";
import ForgetVerifyProcess from "../ForgetVerifyProcess";
export const ForgetPasswordForm = createContext({});

function ForgetPasswordPhone() {
  const history = useHistory();
  const [form] = Form.useForm();
  const handleForgetPasswordPhoneValidator = (forgetPasswordPhoneValidator) => {
    console.log(forgetPasswordPhoneValidator);
    localStorage.setItem(
      "forgetPhoneNumber",
      form.getFieldValue("phoneNumber")
    );

    axios({
      method: "POST",
      url: "/u-consultation/auth/resend-phone-otp",
      data: forgetPasswordPhoneValidator,
    })
      .then((response) => {
        console.log(response);
        history.push("/verify-password-phone");
      })
      .catch((error) => {
        console.log(error);
        if (error.response) {
          console.log(error.response.data.message);
          if (error.response.data.message === "Phone number is not found") {
            message.error("Phone number is not found");
          }
        } else {
          message.error("An error occurred. Please try again.");
        }
      });
  };
  return (
    <div className="forget-password">
      <ForgetPasswordForm.Provider value={form}>
        <Form form={form} onFinish={handleForgetPasswordPhoneValidator}>
          <ForgetVerifyProcess
            head="Forgot Password"
            descreption="Please enter your verified phone number"
            contentInputPhone
            button="Send SMS"
            // footer="Reset password with your verified email address"
            // footerlink="/forget-password-email"
          />
        </Form>
      </ForgetPasswordForm.Provider>
    </div>
  );
}

export default ForgetPasswordPhone;
