import React, { useContext, useEffect, useState } from "react";
import { DatePicker, Select } from "antd";
import { FormContext } from "../AuthProfileComplete";
import AuthInput from "../Inputs/AuthInput/AuthInput";
import AuthInputSelect from "../Inputs/AuthInputSelect";
import AuthMainSubSpecialities from "./AuthMainSubSpecialities";
import AuthInputCountry from "../Inputs/AuthInputCountry/AuthInputCountry";
import AuthAddMoreDynamicForm from "../Inputs/AuthAddMoreDynamicForm/AuthAddMoreDynamicForm";

const { Option } = Select;

const employeeTypeOptions = [
  { value: "full time", data: "Full time" },
  { value: "part time", data: "Part time" },
  { value: "freelance", data: "Freelance" },
  { value: "voluntarily", data: "Voluntarily" },
];

function AuthProfessionalExperience() {
  const [specialty, setSpecialty] = useState([] as any);
  const [subSpecialty, setSubSpecialty] = useState([] as any);

  const professionalExperienceFormItemData = [
    {
      formItemName: "primaryPractice",
      formItemLayout: "11",
      formItemLabel: "Primary Practice",
      key: 3,
      dynamicContent: (
        <div>
          <AuthInput inputText inputTextName="primaryPractice" />
        </div>
      ),
    },
    {
      formItemName: "employeeType",
      formItemLayout: "11",
      formItemLabel: "Employee Type",
      key: 2,
      dynamicContent: <AuthInputSelect inputSelect options={employeeTypeOptions} />,
    },
    {
      formItemName: "mainSpecialty",
      formItemLayout: "11",
      formItemLabel: "Main Specialty",
      key: 4,
      dynamicContent: (
        <Select>
          {specialty.length > 0 &&
            specialty.map((option) => (
              <Option key={option.mainSpecialty} value={option.mainSpecialty}>
                {option.mainSpecialty}
              </Option>
            ))}
        </Select>
      ),
    },
    {
      formItemName: "subSpecialty",
      formItemLayout: "11",
      formItemLabel: "Subspecialty",
      key: 5,
      // dynamicContent: <AuthInputSelect inputSelectMultiple options={subSpecoalityData} />,
      dynamicContent: (
        <Select mode="multiple">
          {subSpecialty.length > 0 &&
            subSpecialty.map((option) =>
              option.subSpecialty.map((item) => (
                <Option key={item} value={item}>
                  {item}
                </Option>
              ))
            )}
        </Select>
      ),
    },
    {
      formItemName: "title",
      formItemLayout: "11",
      formItemLabel: "Title",
      key: 1,
      dynamicContent: (
        <div>
          <AuthInput inputText inputTextName="title" />
        </div>
      ),
    },
    {
      formItemName: "company",
      formItemLayout: "11",
      formItemLabel: "Company",
      key: 6,
      dynamicContent: (
        <div>
          <AuthInput inputText inputTextName="company" />
        </div>
      ),
    },
    {
      formItemName: "country",
      formItemLayout: "11",
      formItemLabel: "Country",
      key: 77,
      dynamicContent: <AuthInputCountry inputCountryName="country" />,
    },
    {
      formItemName: "city",
      formItemLayout: "11",
      formItemLabel: "Region",
      key: 7,
      dynamicContent: <AuthInputSelect inputSelect inputSelectPlaceholder="City" />,
    },
    {
      formItemName: "startYear",
      formItemLayout: "11",
      formItemLabel: "Start Year",
      key: 8,
      dynamicContent: <DatePicker placeholder="Start Year" />,
    },
    {
      formItemName: "endYear",
      formItemLayout: "11",
      formItemLabel: "End Year",
      key: 9,
      dynamicContent: <DatePicker placeholder="End Year" />,
    },
    {
      formItemName: "recentWork",
      formItemLayout: "24",
      key: 10,
      dynamicContent: <AuthInput checkBox checkBoxData="I currently work there" />,
    },
    {
      formItemName: "headLine",
      formItemLayout: "11",
      key: 11,
      dynamicContent: <AuthInput checkBox checkBoxData="Update my headline" />,
    },
    {
      formItemName: "position",
      formItemLayout: "11",
      key: 12,
      dynamicContent: <AuthInput checkBox checkBoxData="Update my position" />,
    },
  ];

  return (
    <div>
      <AuthMainSubSpecialities
        selectedSpecialty={(specialty) => setSpecialty(specialty)}
        selectedSubSpecialty={(subSpecialty) => setSubSpecialty(subSpecialty)}
      />
      <AuthAddMoreDynamicForm
        formListName="professionalExperience"
        formItemData={professionalExperienceFormItemData}
        backgroundStyle={"bg-add-new"}
      />
    </div>
  );
}

export default AuthProfessionalExperience;
