import React, { useState, useContext } from 'react';
import { Tabs, Row, Col, Collapse, Radio, Checkbox } from 'antd';
import AddMedicine from './AddMedicine/AddMedicine';
import UmiInputs from '../../Common/PanelInputs/UmiInputs';
import UmiTextarea from '../../Common/PanelInputs/UmiTextarea';
import ExportExcel from '../../Common/ExportExcel/ExportExcel';
import PanelInputs from '../../Common/PanelInputs/PanelInputs';
import PanelControl from '../../Common/PanelControl/PanelControl';
import SearchInput from '../../Common/PanelInputs/SearchInput/SearchInput';
import EprescriptionReport from '../../Common/EprescriptionReport/EprescriptionReport';
import { FormContext } from '../PatientDetalis';

import './Eprescription.less';

const { TabPane } = Tabs,
  { Panel } = Collapse;

const vacCheckboxArr = ['Hepatitis/Liver Disease', 'Diabetes', 'High Blood Pressure', 'Thyroid Disease', 'Other'];

const displayVacCheckbox = vacCheckboxArr.map((box, i) => {
  return <Checkbox key={i}>{box}</Checkbox>;
});

function Eprescription() {
  const [medicines, setMedicines] = useState([] as any);
  const [lab, setLab] = useState([] as any);
  const [radio, setRadio] = useState([] as any);
  const [consum, setConsum] = useState([] as any);
  const [disabled, setDisabled] = useState(true);

  const form = useContext(FormContext);

  const { getFieldValue } = form;

  const ExportExcelRender = () => <ExportExcel activeTab='prescription' tabName='E-prescription' />;

  const editMedicineInfo = (index: number) => {
    medicines.splice(index, 1);
    setMedicines([...medicines]);
    setDisabled(false);
  };

  const editLabInfo = (index: number) => {
    lab.splice(index, 1);
    setLab([...lab]);
    setDisabled(false);
  };

  const editRadioInfo = (index: number) => {
    radio.splice(index, 1);
    setRadio([...radio]);
    setDisabled(false);
  };

  const editConsumInfo = (index: number) => {
    consum.splice(index, 1);
    setConsum([...consum]);
    setDisabled(false);
  };

  const removeMedicineInfo = (index: number) => {
    medicines.splice(index, 1);
    setMedicines([...medicines]);
  };

  const removeLabInfo = (index: number) => {
    lab.splice(index, 1);
    setLab([...lab]);
  };

  const removeRadioInfo = (index: number) => {
    radio.splice(index, 1);
    setRadio([...radio]);
  };

  const removeConsumInfo = (index: number) => {
    consum.splice(index, 1);
    setConsum([...consum]);
  };

  const validateAddMedicineForm = () => {
    setDisabled(
      !getFieldValue('prescription-medicine') ||
        !getFieldValue('medicine-capsules') ||
        !getFieldValue('medicine-times') ||
        !getFieldValue('medicine-weeks')
    );
  };

  const validateAddLabForm = () => {
    setDisabled(!getFieldValue('prescription-laboratory'));
  };

  const validateAddRadioForm = () => {
    setDisabled(!getFieldValue('prescription-radiology') || !getFieldValue('radiology-investigation'));
  };

  const validateAddConsumForm = () => {
    setDisabled(!getFieldValue('prescription-consumable'));
  };

  const addNewMedicine = (childData) => {
    setMedicines([...medicines, childData]);
    setDisabled(true);
  };

  const addNewLab = (childData) => {
    setLab([...lab, childData]);
    setDisabled(true);
  };

  const addNewRadio = (childData) => {
    setRadio([...radio, childData]);
    setDisabled(true);
  };

  const addNewConsum = (childData) => {
    setConsum([...consum, childData]);
    setDisabled(true);
  };

  return (
    <Tabs defaultActiveKey='medication' className='umi-eprescription-wrapper'>
      <TabPane tab='Medication' key='medication'>
        <Row gutter={10}>
          <Col span={12}>
            <Collapse defaultActiveKey={['medication']} bordered={false}>
              <Panel header='Medication' key='medication'>
                <PanelControl moreButton panelKey='medication-prescription'>
                  <ExportExcelRender />
                </PanelControl>
                <PanelInputs>
                  <div className='title-add-wrapper'>
                    <div className='umi-title'>Needed Medicines</div>
                    <AddMedicine parentCallback={addNewMedicine} disabled={disabled} medicine />
                  </div>
                  <SearchInput
                    name='prescription-medicine'
                    placeholder='Medicine Name'
                    handleChange={validateAddMedicineForm}
                  />
                  <div className='inputs-container'>
                    <UmiInputs
                      type='number'
                      name='medicine-capsules'
                      placeholder='No. Of Capsules'
                      handleChange={validateAddMedicineForm}
                    />
                    <UmiInputs
                      type='number'
                      name='medicine-times'
                      placeholder='No. Of Times'
                      handleChange={validateAddMedicineForm}
                    />
                    <UmiInputs
                      type='number'
                      name='medicine-weeks'
                      placeholder='No. Of Weeks'
                      handleChange={validateAddMedicineForm}
                    />
                  </div>
                  <div className='umi-title'>Speacial Instructions</div>
                  <UmiTextarea name='medicine-notes' placeholder='Notes' />
                </PanelInputs>
              </Panel>
            </Collapse>
          </Col>
          <Col span={12}>
            <EprescriptionReport
              medicalReport
              passMedicines={medicines}
              editMedicineInfo={editMedicineInfo}
              removeMedicineInfo={removeMedicineInfo}
            />
          </Col>
        </Row>
      </TabPane>
      <TabPane tab='Laboratory' key='laboratory'>
        <Row gutter={10}>
          <Col span={12}>
            <Collapse defaultActiveKey={['laboratory']} bordered={false}>
              <Panel header='Laboratory' key='laboratory'>
                <PanelControl moreButton panelKey='laboratory-prescription'>
                  <ExportExcelRender />
                </PanelControl>
                <PanelInputs>
                  <div className='title-add-wrapper'>
                    <div className='umi-title'>Needed Laboratory</div>
                    <AddMedicine parentCallback={addNewLab} disabled={disabled} lab />
                  </div>
                  <SearchInput
                    name='prescription-laboratory'
                    placeholder='Laboratory Name'
                    handleChange={validateAddLabForm}
                  />
                  <div className='umi-title'>Speacial Instructions</div>
                  <div className='umi-lab-radio'>
                    <span className='umi-radio-label'>Fasting Required</span>
                    <Radio.Group name='radiogroup' defaultValue='yes'>
                      <Radio value='yes'>Yes</Radio>
                      <Radio value='no'>No</Radio>
                    </Radio.Group>
                  </div>
                  <UmiTextarea name='laboratory-notes' placeholder='Notes' />
                </PanelInputs>
              </Panel>
            </Collapse>
          </Col>
          <Col span={12}>
            <EprescriptionReport labReport passLab={lab} editLabInfo={editLabInfo} removeLabInfo={removeLabInfo} />
          </Col>
        </Row>
      </TabPane>
      <TabPane tab='Radiology' key='radiology'>
        <Row gutter={10}>
          <Col span={12}>
            <Collapse defaultActiveKey={['radiology']} bordered={false}>
              <Panel header='Radiology' key='radiology'>
                <PanelControl moreButton panelKey='radiology-prescription'>
                  <ExportExcelRender />
                </PanelControl>
                <PanelInputs>
                  <div className='title-add-wrapper'>
                    <div className='umi-title'>Needed Radiology</div>
                    <AddMedicine parentCallback={addNewRadio} disabled={disabled} radio />
                  </div>
                  <SearchInput
                    name='prescription-radiology'
                    placeholder='Radiology Name'
                    handleChange={validateAddRadioForm}
                  />
                  <SearchInput
                    name='radiology-investigation'
                    placeholder='Radiology Investigation:'
                    handleChange={validateAddRadioForm}
                  />
                  <div className='umi-title'>Speacial Instructions</div>
                  <SearchInput
                    name='organ-scan'
                    placeholder="Organ To Be Scanned Fron The Patient's Body"
                    handleChange={validateAddRadioForm}
                  />
                  <div className='inputs-container'>
                    <SearchInput name='radiology-tincture' placeholder='Tincture' handleChange={validateAddRadioForm} />
                    <SearchInput
                      name='radiology-anesthesia'
                      placeholder='Anesthesia'
                      handleChange={validateAddRadioForm}
                    />
                  </div>
                  <SearchInput
                    name='radiology-labs'
                    placeholder='Additional Labs'
                    handleChange={validateAddRadioForm}
                  />
                  <Checkbox>Other instructions</Checkbox>
                  <UmiTextarea name='radiology-notes' placeholder='Notes' />
                </PanelInputs>
              </Panel>
            </Collapse>
          </Col>
          <Col span={12}>
            <EprescriptionReport
              radioReport
              passRadio={radio}
              editRadioInfo={editRadioInfo}
              removeRadioInfo={removeRadioInfo}
            />
          </Col>
        </Row>
      </TabPane>
      <TabPane tab='Consumable' key='consumable'>
        <Row gutter={10}>
          <Col span={12}>
            <Collapse defaultActiveKey={['consumable']} bordered={false}>
              <Panel header='Consumable' key='consumable'>
                <PanelControl moreButton panelKey='consumable-prescription'>
                  <ExportExcelRender />
                </PanelControl>
                <PanelInputs>
                  <div className='title-add-wrapper'>
                    <div className='umi-title'>Needed Consumable</div>
                    <AddMedicine parentCallback={addNewConsum} disabled={disabled} consum />
                  </div>
                  <SearchInput
                    name='prescription-consumable'
                    placeholder='Consumable Name'
                    handleChange={validateAddConsumForm}
                  />
                  <div className='umi-title'>Speacial Instructions</div>
                  <div className='umi-lab-radio'>
                    <span className='umi-radio-label'>Fasting Required</span>
                    <Radio.Group name='radiogroup' defaultValue='yes'>
                      <Radio value='yes'>Yes</Radio>
                      <Radio value='no'>No</Radio>
                    </Radio.Group>
                  </div>
                  <UmiTextarea name='consumable-notes' placeholder='Notes' />
                </PanelInputs>
              </Panel>
            </Collapse>
          </Col>
          <Col span={12}>
            <EprescriptionReport
              consumReport
              passConsum={consum}
              editConsumInfo={editConsumInfo}
              removeConsumInfo={removeConsumInfo}
            />
          </Col>
        </Row>
      </TabPane>
      <TabPane tab='Vaccination' key='vaccination'>
        <Row gutter={10}>
          <Col span={12}>
            <Collapse defaultActiveKey={['vaccination']} bordered={false}>
              <Panel header='Vaccination' key='vaccination'>
                <PanelControl moreButton panelKey='vaccination-prescription'>
                  <ExportExcelRender />
                </PanelControl>
                <PanelInputs>
                  <div className='umi-title'>Needed Vaccination</div>
                  <div className='umi-vic-checkbox'>{displayVacCheckbox}</div>
                  <UmiTextarea name='vaccination-notes' placeholder='Notes' />
                  <div className='umi-title'>Speacial Instructions</div>
                  <div className='umi-lab-radio'>
                    <span className='umi-radio-label'>Fasting Required</span>
                    <Radio.Group name='radiogroup' defaultValue='yes'>
                      <Radio value='yes'>Yes</Radio>
                      <Radio value='no'>No</Radio>
                    </Radio.Group>
                  </div>
                  <UmiTextarea name='vaccination-instructions-notes' placeholder='Notes' />
                </PanelInputs>
              </Panel>
            </Collapse>
          </Col>
          <Col span={12}>
            <EprescriptionReport vacReport />
          </Col>
        </Row>
      </TabPane>
    </Tabs>
  );
}

export default Eprescription;
