import React from 'react';
import { Descriptions } from 'antd';

import './PatientData.less';

type Iprops = {
  additionData?: boolean;
  title?: boolean;
  col?: number;
};

function PatientData({ title, additionData, col }: Iprops) {
  return (
    <>
      {title && <div className="title">Patient Information</div>}
      <Descriptions column={col}>
        <Descriptions.Item label="Patient Name">Ahmed Hassan</Descriptions.Item>
        <Descriptions.Item label="Patient ID">2151516</Descriptions.Item>
        <Descriptions.Item label="Reservation Date">12/8/2019</Descriptions.Item>
        <Descriptions.Item label="Date of Birth">5/8/1989</Descriptions.Item>
        {additionData && (
          <>
            <Descriptions.Item label="Weight">89 Kg</Descriptions.Item>
            <Descriptions.Item label="Blood Pressure">Low</Descriptions.Item>
            <Descriptions.Item label="Insurance Status">None</Descriptions.Item>
            <Descriptions.Item label="Gender">Male</Descriptions.Item>
            <Descriptions.Item label="Blood Group">B+</Descriptions.Item>
            <Descriptions.Item label="Type">New</Descriptions.Item>
            <Descriptions.Item label="Height">190 Cm</Descriptions.Item>
          </>
        )}
      </Descriptions>
    </>
  );
}

export default PatientData;
