import React from "react";
import { Steps, Progress } from "antd";
import "./AuthAsideSteps.less";
const { Step } = Steps;

function AuthAsideSteps({ accountPercentage }) {
  return (
    <div className="auth-aside-steps">
      <Steps current={0} labelPlacement="vertical" size='small'>
        <Step
          description="Account Created"
          icon={
            <Progress
              type="circle"
              width={30}
              className='last'
              percent={accountPercentage}
              style={{ marginLeft: '-8px', marginTop: '-8px' }}
              format={percent => ""}
            />
          }
        />
        <Step description="Under Review" />
        <Step description="Approved" />
      </Steps>
    </div>
  );
}

export default AuthAsideSteps;
