import React, { useState, createContext } from "react";
import { Row, Col, Button } from "antd";
import { Link } from "react-router-dom";
import AuthLayout from "../../AuthCommon/AuthLayout";
import "../MainProcessUtils/Styling/Signup&Login.less";
import "../MainProcessUtils/Styling/SignupOptions.less";
import SignupOptions from "./SignupOptions/SignupOptions";

export const SignupOptionType = createContext("");

function Signup() {
  const [patient, setPatient] = useState("");
  const [signupOption, setSignupOption] = useState("serviceProvider");
  console.log(signupOption);
  return (
    <div className="signup-options">
      <SignupOptionType.Provider value="serviceProvider">
        <Row>
          <Col span="8">
            <AuthLayout />
          </Col>
          <Col span="16">
            <div className="umi-auth-form ">
              <img
                src="imgs/logo_icon.png"
                alt="umedmi"
                className="form-logo"
              />
              <h1>Register as</h1>

              <div className="signup-options-content">
                <div
                  className={
                    patient === "false"
                      ? "signup-options-card signup-focus-card"
                      : "signup-options-card"
                  }
                  onClick={() => {
                    setSignupOption("serviceProvider");
                    setPatient("false");
                  }}
                >
                  <img
                    src="imgs/icons/auth/portfolio-lg.svg"
                    alt="service provider"
                  />
                  <p> Service Provider</p>
                </div>

                <div
                  className={
                    patient === "true"
                      ? "signup-options-card signup-focus-card"
                      : "signup-options-card"
                  }
                  onClick={() => {
                    setSignupOption("patient");
                    setPatient("true");
                  }}
                >
                  <img src="imgs/icons/auth/patient.svg" alt="patient" />
                  <p> Patient</p>
                </div>
              </div>
              <div>
                <Link to="/signup-options">
                  <Button style={{ marginTop: "30px", width: "200px" }}>
                    next
                  </Button>
                </Link>
              </div>
            </div>
          </Col>
        </Row>
      </SignupOptionType.Provider>
    </div>
  );
}
export default Signup;

