import * as React from 'react';
import { Table, Tabs } from 'antd';
import { FlagOutlined } from '@ant-design/icons';

import './MedicalHistory.less';

const { TabPane } = Tabs;

const columns = [
  {
    title: 'Allergy',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Allergy Reaction',
    dataIndex: 'age',
    key: 'age',
  },
  {
    title: 'Doctor Name',
    dataIndex: 'address',
    key: 'address',
  },
  {
    title: 'Outcome',
    dataIndex: 'outcome',
    key: 'outcome',
  },
  {
    title: 'Red flag',
    dataIndex: 'flag',
    key: 'flag',
    render: (flag: boolean) => flag && <FlagOutlined />,
  },
];

const data = [
  {
    key: '1',
    name: 'Mold',
    age: 'Have you ever felt like you or someone you know is constantly coughing and sneezing?',
    address: 'Ahmed Hassan',
    outcome: 'Chronic',
    flag: true,
  },
  {
    key: '2',
    name: 'Red Eyes',
    age: 'Have you ever felt like you or someone you know is constantly coughing and sneezing?',
    address: 'Ahmed Hassan',
    outcome: 'Resolved',
    flag: true,
  },
  {
    key: '3',
    name: 'Drug',
    age: 'Have you ever felt like you or someone you know is constantly coughing and sneezing?',
    address: 'Ahmed Hassan',
    outcome: 'Current',
    flag: false,
  },
  {
    key: '4',
    name: 'latex',
    age: 'Have you ever felt like you or someone you know is constantly coughing and sneezing?',
    address: 'Ahmed Hassan',
    outcome: 'Chronic',
    flag: false,
  },
];

function MedicalHistory() {
  return (
    <div className="umi-medical-history-wrapper">
      <Tabs defaultActiveKey="1">
        <TabPane tab="Allergies" key="1" />
        <TabPane tab="Medication" key="2" />
        <TabPane tab="Surgeries" key="3" />
        <TabPane tab="Vaccination" key="4" />
        <TabPane tab="System Review" key="5" />
        <TabPane tab="Other Spcialities" key="6" />
      </Tabs>
      <Table columns={columns} dataSource={data} pagination={false} />
    </div>
  );
}

export default MedicalHistory;
