import React, { useState } from 'react';
import moment from 'moment';
import { DatePicker } from 'antd';
import MainComplain from './MainComplain/MainComplain';

import './SessionHistory.less';

const { RangePicker } = DatePicker;

function onChange(dates, dateStrings) {
  console.log('From: ', dates[0], ', to: ', dates[1]);
  console.log('From: ', dateStrings[0], ', to: ', dateStrings[1]);
}

function SessionHistory({ sendText }) {
  const [patientInfo, setPatientInfo] = useState();

  const changeActiveKey: any = () => sendText('patient-information');

  return (
    <>
      <div className="umi-datepicker-new">
        <div className="umi-datepicker">
          <div>Date</div>
          <RangePicker
            ranges={{
              Today: [moment(), moment()],
              'This Month': [moment().startOf('month'), moment().endOf('month')],
            }}
            onChange={onChange}
          />
          <div className="umi-cards-count">5 Cards</div>
        </div>
        <div className="umi-btn-wrapper">
          <button className="umi-btn small umi-btn-complete" onClick={changeActiveKey}>
            Open New Session
          </button>
        </div>
      </div>
      <div className="umi-session-complain">
        <MainComplain more changeActiveKey={changeActiveKey} />
        <MainComplain more changeActiveKey={changeActiveKey} />
        <MainComplain draft complete />
      </div>
      <div className="umi-btn-wrapper">
        <button className="umi-btn umi-btn-complete">Load More</button>
      </div>
    </>
  );
}

export default SessionHistory;
