import React from "react";
import "./MainInvitationAside.less";
import AuthAds from "../../../../../AuthCommon/AuthAds";
import InvitationShare from "./InvitationShare/InvitationShare";

function MainInvitationAside() {
  return (
    <div className="main-invitation-aside">
      <div className="invitation-description">
        <h1>FREE GOLD MONTH!</h1>
        <p>
          Invite 10 colleagues or more and get for 1 free month of Gold
          subscription.
        </p>
      </div>
      <div className="invitation-code">
        <InvitationShare />
      </div>

      <>
        <AuthAds />
      </>
    </div>
  );
}

export default MainInvitationAside;
