import React from "react";
import { Button } from "antd";
import "./AuthSocialMedia.less";

function AuthUmedMiAccount(props) {
  return (
    <div className="auth-umedmi">
      <Button
        type="link"
        icon={
          <img
            src="/imgs/icons/auth/socialMedia/umedmi.png"
            alt="UmedMi"
            style={{ width: "24px", height: "24px" }}
          />
        }
        size="small"
      ></Button>
    </div>
  );
}
export default AuthUmedMiAccount;
