import React, { useState, Fragment } from 'react';
import { Modal, Form, Radio, Input } from 'antd';
import { FileDoneOutlined, SearchOutlined } from '@ant-design/icons';

import './TemplateList.less';

const favoriteRadio = [
  { id: 1, value: 'first', label: 'First' },
  { id: 2, value: 'second', label: 'Second' },
  { id: 3, value: 'third', label: 'Third' },
  { id: 4, value: 'fourth', label: 'Fourth' },
  { id: 5, value: 'fifth', label: 'Fifth' },
  { id: 6, value: 'sixth', label: 'Sixth' },
  { id: 7, value: 'seventh', label: 'Seventh' },
];

function TemplateList() {
  const [modalVisible, setModalVisible] = useState(false);
  const [filter, setFilter] = useState('');

  const onFinish = (values) => {
    console.log('Received values of form: ', values);
  };

  return (
    <>
      <div className="template-list" onClick={() => setModalVisible(true)}>
        <FileDoneOutlined />
      </div>
      <Modal
        title="Select a Template you want to favorite"
        centered
        visible={modalVisible}
        onOk={() => setModalVisible(false)}
        onCancel={() => setModalVisible(false)}
      >
        <Form name="template-list" onFinish={onFinish}>
          <div className="input-search">
            <Input value={filter} onChange={(e) => setFilter(e.target.value)} />
            <SearchOutlined />
          </div>
          <Form.Item name="radio-group">
            <Radio.Group>
              {favoriteRadio.map((radio) => (
                <Fragment key={radio.id}>
                  {filter.length !== 0 && (
                    <>
                      {radio.label.toLowerCase().includes(filter.toLowerCase()) && (
                        <Radio value={radio.value}>{radio.label}</Radio>
                      )}
                    </>
                  )}
                  {filter.length === 0 && <Radio value={radio.value}>{radio.label}</Radio>}
                </Fragment>
              ))}
            </Radio.Group>
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
}

export default TemplateList;
