import React from "react";
import { Row, Col, Input, Form } from "antd";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";

type Iprops = {
  inputPhoneLabel?: string;
  inputPhoneValidator?: any;
  phoneValidatorMessage?: string;
};

function AuthPhoneInput({
  inputPhoneValidator,
  phoneValidatorMessage,
}: Iprops) {
  return (
    <div>
      <Form.Item
        name="phoneNumber"
        rules={[
          {
            validator(rule, value) {
              const reg = new RegExp(`${inputPhoneValidator}`);
              if (reg.test(value)) {
                return Promise.resolve();
              }
              return Promise.reject(`${phoneValidatorMessage}`);
            },
          },
        ]}
        hasFeedback
      >
        <Row className="phone-group">
          <Col span={5} className="phone-group-flag">
            <PhoneInput country={"eg"} />
          </Col>
          <Col span={19} className="phone-group-input">
            <Form.Item>
              <Input placeholder="Phone Number" />
            </Form.Item>
          </Col>
        </Row>
      </Form.Item>
    </div>
  );
}

export default AuthPhoneInput;
