import React, { useState, useRef, useCallback } from "react";
import Geocode from "react-geocode";
import {
  GoogleMap,
  LoadScript,
  Marker,
  InfoWindow,
} from "@react-google-maps/api";
import usePlacesAutocomplete, {
  getGeocode,
  getLatLng,
} from "use-places-autocomplete";

import {
  Combobox,
  ComboboxInput,
  ComboboxPopover,
  ComboboxList,
  ComboboxOption,
} from "@reach/combobox";

import "@reach/combobox/styles.css";
import "./Map.less";
const mapContainerStyle = {
  height: "60vh",
  width: "100%",
};
const options = {
  disableDefaultUI: true,
  zoomControl: true,
};

function Map() {
  Geocode.setApiKey("AIzaSyAfdttrEL8vruqeqnx5snWGqekQDxLGJyM");
  const [center, setCenter] = useState({
    lat: 30.033333,
    lng: 31.233334,
  });
  const [markers, setMarkers] = useState([
    {
      lat: 30.033333,
      lng: 31.233334,
    },
  ]);
  const [selected, setSelected] = useState({ lat: 20, lng: 20 });
  console.log(markers);

  navigator.geolocation.getCurrentPosition(function(position) {
    setCenter({
      lat: position.coords.latitude,
      lng: position.coords.longitude,
    });
    setMarkers([
      { lat: position.coords.latitude, lng: position.coords.longitude },
    ]);
  });

  Geocode.fromLatLng(markers[0].lat, markers[0].lng).then(
    (response) => {
      const geoCodeAddress = response.results[0].formatted_address;
      console.log(geoCodeAddress);
      localStorage.setItem("geoCodeAddress", geoCodeAddress);
    },
    (error) => {
      console.error(error);
    }
  );

  const onMapClick = useCallback((e) => {
    setMarkers(() => [
      {
        lat: e.latLng.lat(),
        lng: e.latLng.lng(),
      },
    ]);
  }, []);

  // const onMapLoad = useCallback((map) => {
  //   mapRef.current = map;
  // }, []);
  // const panTo = useCallback(({ lat, lng }) => {
  //   mapRef.current.panTo({ lat, lng });
  // }, []);
  const mapRef = useRef({ panTo });
  function onMapLoad(map) {
    mapRef.current = map;
  }

  function panTo({ lat, lng }) {
    mapRef.current.panTo({ lat, lng });
  }

  return (
    <div className="map">
      <h3>Move pin to choose location</h3>
      <Search panTo={panTo} />

      <GoogleMap
        id="map"
        mapContainerStyle={mapContainerStyle}
        options={options}
        zoom={18}
        center={center}
        onClick={onMapClick}
        onLoad={onMapLoad}
      >
        {markers.map((marker) => (
          <Marker
            key={`${marker.lat}-${marker.lng}`}
            position={{ lat: marker.lat, lng: marker.lng }}
          />
        ))}

        {selected ? (
          <InfoWindow
            position={{ lat: selected.lat, lng: selected.lng }}
            onCloseClick={() => {
              setSelected({ lat: 20, lng: 20 });
            }}
          >
            <div>
              <p>Spotted </p>
            </div>
          </InfoWindow>
        ) : null}
      </GoogleMap>
    </div>
  );
}

function Search({ panTo }) {
  const {
    ready,
    value,
    suggestions: { status, data },
    setValue,
    clearSuggestions,
  } = usePlacesAutocomplete();

  const handleInput = (e) => {
    setValue(e.target.value);
  };

  const handleSelect = async (address) => {
    setValue(address, false);
    clearSuggestions();

    try {
      const results = await getGeocode({ address });
      const { lat, lng } = await getLatLng(results[0]);
      panTo({ lat, lng });
    } catch (error) {
      console.log("Error: ", error);
    }
  };

  return (
    <div className="search">
      <Combobox onSelect={handleSelect}>
        <ComboboxInput
          value={value}
          onChange={handleInput}
          disabled={!ready}
          placeholder="Search"
        />
        <ComboboxPopover>
          <ComboboxList>
            {status === "OK" &&
              data.map(({ id, description }) => (
                <ComboboxOption key={id} value={description} />
              ))}
          </ComboboxList>
        </ComboboxPopover>
      </Combobox>
    </div>
  );
}
export default Map;
