import React from 'react';
import { Collapse } from 'antd';
import UmiInputs from '../../Common/PanelInputs/UmiInputs';
import MedicalHistory from '../MedicalHistory/MedicalHistory';
import UmiTextarea from '../../Common/PanelInputs/UmiTextarea';
import PanelInputs from '../../Common/PanelInputs/PanelInputs';
import ExportExcel from '../../Common/ExportExcel/ExportExcel';
import PanelControl from '../../Common/PanelControl/PanelControl';
import PatientInfoOthers from '../PatientInfoOthers/PatientInfoOthers';
import PatientData from '../../Common/UmiPrint/PatientData/PatientData';
import SearchInput from '../../Common/PanelInputs/SearchInput/SearchInput';

import './PatientInformation.less';

const { Panel } = Collapse;

function PatientInformation() {
  const ExportExcelRender = () => <ExportExcel activeTab="patientInformation" tabName="Patient Information" />;

  return (
    <>
      <div className="umi-patient-informtaion-wrapper">
        <Collapse defaultActiveKey={['presenting-complain']} bordered={false}>
          <Panel header="Presenting Complain" key="presenting-complain">
            <PanelControl moreButton panelKey="presenting-complain">
              <ExportExcelRender />
            </PanelControl>
            <PanelInputs>
              <div className="inputs-container">
                <SearchInput name="subject" placeholder="Subject" />
                <UmiInputs type="number" name="hash" placeholder="#" />
              </div>
              <UmiTextarea name="description" placeholder="Write The Main Complain ..." />
            </PanelInputs>
          </Panel>
          <Panel header="Profile Information" key="profile-information">
            <PanelControl>
              <ExportExcelRender />
            </PanelControl>
            <PatientData additionData />
          </Panel>
          <Panel header="Medical History" key="medical-history">
            <PanelControl moreButton panelKey="medical-history">
              <ExportExcelRender />
            </PanelControl>
            <MedicalHistory />
          </Panel>
          <Panel header="Others" key="others">
            <PanelControl moreButton panelKey="others">
              <ExportExcelRender />
            </PanelControl>
            <PatientInfoOthers />
          </Panel>
        </Collapse>
      </div>
    </>
  );
}

export default PatientInformation;
