import React, { useContext } from "react";
import { Row, Col, Form, DatePicker, Input } from "antd";
import AuthInput from "../../Inputs/AuthInput/AuthInput";
import AuthInputUpload from "../../Inputs/AuthUpload/AuthInputUpload";
import AuthInputRadio from "../../Inputs/AuthInputRadio/AuthInputRadio";
import AuthPersonalInfoId from "./AuthPersonalInfoId/AuthPersonalInfoId";
import { GenderData, TitleData } from "../SecondStepData/PersonallInfoData";
import AuthPersonalInfoEmail from "./AuthPersonalInfoEmail/AuthPersonallInfoEmail";
import AuthPersonalInfoPhones from "./AuthPersonalInfoPhones/AuthPersonallInfoPhones";
import AuthPersonalInfoInterests from "./AuthPersonalInfoInterests/AuthPersonalInfoInterests";
import { FormContext } from "../../AuthProfileComplete";

function AuthPersonalInfo() {
  const form = useContext(FormContext);
  const genderData = GenderData;
  const titleData = TitleData;
  return (
    <div className="auth-personal-info">
      <div className="bg-add-new">
        <Row justify="space-between">
          <Col span={24}>
            <AuthInputUpload />
          </Col>
          <Col span={24}>
            <div className="auth-custom-radio">
              <AuthInputRadio
                radioName="title"
                radioLabel="Title"
                radioOptions={titleData}
              />
            </div>
          </Col>

          <Col span={8}>
            <AuthInput
              inputText
              inputTextName={["fullName", "firstName"]}
              inputTextLabel="First Name"
            />
          </Col>
          <Col span={7}>
            <AuthInput
              inputText
              inputTextName={["fullName", "middleName"]}
              inputTextLabel="Middle Name"
            />
          </Col>
          <Col span={7}>
            <AuthInput
              inputText
              inputTextName={["fullName", "lastName"]}
              inputTextLabel="Last Name"
            />
          </Col>
          <Col span={24}>
            <AuthInput
              inputText
              inputTextName="fullName"
              inputTextLabel="Full Name (In Arabic)"
            />
          </Col>

          <Col span={8}>
            <div className="auth-custom-radio">
              <AuthInputRadio
                radioName="gender"
                radioLabel="Gender"
                radioOptions={genderData}
              />
            </div>
          </Col>
          <Col span={15}>
            <Form.Item name="dateOfBirth" style={{ display: "none" }}>
              <Input />
            </Form.Item>
            <Form.Item label="Date of Birth">
              <DatePicker
                onChange={(value, dateString) => {
                  form.setFieldsValue({ dateOfBirth: dateString });
                }}
              />
            </Form.Item>
          </Col>
          <Col span={24}>
            <AuthPersonalInfoId />
          </Col>

          <Col span={24}>
            <AuthPersonalInfoEmail />
          </Col>

          <Col span={24}>
            <AuthPersonalInfoPhones />
          </Col>
        </Row>
      </div>

      <AuthPersonalInfoInterests />
    </div>
  );
}

export default AuthPersonalInfo;
