import React from "react";
import ForgetVerifyProcess from "../ForgetVerifyProcess";
function ForgetPasswordEmail() {
  return (
    <div className="forget-password">
      <ForgetVerifyProcess
        head="Forgot Password"
        descreption="Please enter your email address below. We will email you a password resent link."
        contentInputEmail
        button="Email Me"
        footer="Send SMS Instead"
        footerlink="/forget-password-phone"
      />
    </div>
  );
}

export default ForgetPasswordEmail;
