import React from "react";
import { Select, Form } from "antd";
const { Option } = Select;
type Iprops = {
  inputSelect?: any;
  inputSelectName?: any;
  inputSelectPlaceholder?: string;
  inputSelectLabel?: string;
  options?: any;
  inputSelectMultiple?: any;
};

function AuthInputSelect({
  inputSelect,
  inputSelectName,
  inputSelectPlaceholder,
  options,
  inputSelectLabel,
  inputSelectMultiple,
}: Iprops) {
  return (
    <div className="auth-input">
      {inputSelect && (
        <Form.Item name={inputSelectName} label={inputSelectLabel}>
          <Select placeholder={inputSelectPlaceholder}>
            {options && (
              <>
                {options.map((option) => (
                  <React.Fragment key={option.value}>
                    <Option value={option.value}>{option.data}</Option>
                  </React.Fragment>
                ))}
              </>
            )}
          </Select>
        </Form.Item>
      )}
      {inputSelectMultiple && (
        <Form.Item name={inputSelectName}>
          <Select
            placeholder={inputSelectPlaceholder}
            mode="multiple"
            // defaultOpen={true} //to customize css
          >
            {options && (
              <>
                {options.map((option) => (
                  <React.Fragment key={option.value}>
                    <Option value={option.value} key={option.value}>
                      {option.data}
                    </Option>
                  </React.Fragment>
                ))}
              </>
            )}
          </Select>
        </Form.Item>
      )}
    </div>
  );
}
export default AuthInputSelect;

