import React, { useEffect, useState } from "react";
import axios from "axios";
import { EducationFormItemData } from "./ThirdStepData/EducationCertificatesData";
import AuthAddMoreDynamicForm from "../Inputs/AuthAddMoreDynamicForm/AuthAddMoreDynamicForm";
import { Select, Form } from "antd";
const { Option } = Select;

function AuthEducationCertificates() {
  const educationFormItemData = EducationFormItemData;
  const [institute, setInstitute] = useState([{ name: "", countryId: "" }]);
  const instituteData = [] as any;
  const [selectOption, setSelectOption] = useState("");
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    axios.get("/u-consultation/institute").then((response) => {
      setInstitute(response.data.data);

      setLoading(false);
    });
  }, []);
  const testPostData = (value) => {
    const data = {
      name: value,
      countryId: "5f303f8b583b8b01362a0e52",
    };
    axios.post("/u-consultation/institute", data);
  };

  const handleSelectData = (selectedValue) => {
    institute.map((data) => {
      instituteData.push(data.name);
    });

    if (!instituteData.includes(selectedValue)) {
      testPostData(selectedValue);
    }
  };
  const instituteObj = {
    formItemName: "instituteId",
    formItemLabel: "Institute",
    formItemLayout: "11",
    dynamicContent: (
      <Select
        showSearch={true}
        onSearch={(value) => setSelectOption(value)}
        onChange={(value) => handleSelectData(value)}
      >
        {institute.map((data, index) => (
          <Option value={data.name} key={index}>
            {data.name}
          </Option>
        ))}
        <Option value={selectOption}>{selectOption}</Option>
      </Select>
    ),
    key: 1888,
  };
  if (!loading) {
    educationFormItemData.splice(2, 0, instituteObj);
  }
  return (
    <div className="auth-education">
      {!loading && (
        <AuthAddMoreDynamicForm
          formItemData={educationFormItemData}
          formListName="education"
          backgroundStyle="bg-add-new"
        />
      )}
    </div>
  );
}

export default AuthEducationCertificates;
