import React from "react";
import { Iprops } from "./SubscriptionCardTypes";
import "./SubscriptionCard.less";

function SubscriptionCard({
  subscriptionType,
  price,
  messages,
  sharing,
  storage,
  sharingPatients,
  viewSharedPatients,
  pollFeature,
  messageDeletion,
  umedmiBot,
  videoCall,
  scheduleEvents,
  adsBlocker,
  typeColor,
}: Iprops) {
  return (
    <div className="subscription-card">
      <div className="subscription-type">
        <p style={{ background: `${typeColor}` }}>{subscriptionType}</p>
      </div>
      <div className="subscription-details">
        <span className="price">{price}</span>
        <p className="messages">{messages}</p>
        <p className="storage">{storage}</p>
        <p className="sharing">{sharing}</p>
        <p className="sharing-patients">{sharingPatients}</p>
        <p className="view-shared-patients">{viewSharedPatients}</p>
        <p className="poll-feature">{pollFeature}</p>
        <p className="message-deletion">{messageDeletion}</p>
        <p className="umedmi-bot">{umedmiBot}</p>
        <p className="call-video">{videoCall}</p>
        <p className="schedule-events">{scheduleEvents}</p>
        <p className="ads-blocker">{adsBlocker}</p>
      </div>
    </div>
  );
}

export default SubscriptionCard;
