import React, { useContext } from 'react';
import { Card } from 'antd';
import { FormContext } from '../../PatientDetalis';
import Status from '../../../Common/Status/Status';

import './MainComplain.less';

type IProps = {
  draft?: any;
  more?: boolean;
  complete?: any;
  viewSession?: Function;
  changeActiveKey?: any;
  resetFields?: Function;
};

function MainComplain({ draft, more, complete, changeActiveKey, resetFields }: IProps) {
  const form = useContext(FormContext);

  const editComplain = () => {
    changeActiveKey();
    form.setFieldsValue({
      subject: 'fever',
      hash: '25971',
      description:
        'it is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout',
    });
  };

  return (
    <Card
      size="small"
      title="Main Complain"
      extra={<a href="#">#</a>}
      style={{ width: 330 }}
      className={draft && 'umi-complain-draft'}
    >
      {draft && <Status title="Draft" />}
      <ul>
        <li>
          <div className="umi-title-date">
            <div className="umi-title">Session Date</div>
            <div className="umi-date">12/5/2019</div>
          </div>
          <div className="umi-months">6 Mon Ago</div>
        </li>
        <li>
          <div className="umi-title-date">
            <div className="umi-title">Revision Date</div>
            <div className="umi-date">25/5/2019</div>
          </div>
        </li>
        <li>Medical Examination</li>
        <li>Interventions</li>
      </ul>
      <div className="umi-btn-wrapper">
        {more && (
          <button className="umi-btn" onClick={editComplain}>
            View More
          </button>
        )}
        {complete && <button className="umi-btn umi-btn-complete">Complete Session</button>}
      </div>
    </Card>
  );
}

export default MainComplain;
