import React, { createContext } from 'react';
import axios from 'axios';
import jwtDecode from 'jwt-decode';
import { useHistory } from 'react-router-dom';
import { Form, Row, Col, message } from 'antd';
import LoginForm from './LoginForm';
import AuthLayout from '../../AuthCommon/AuthLayout';
import AuthSocialMedia from '../AuthSocialMedia/AuthSocialMedia';
import PolicyAndTerms from '../PolicyAndTerms/PolicyAndTermsShared/PolicyAndTermsShared';

import '../MainProcessUtils/Styling/Signup&Login.less';
import { loginErrors } from './LoginErrors';

export const LoginFormContext = createContext({} as any);

function Login() {
  const history = useHistory();
  const [form] = Form.useForm();
  const initialValues = { type: 'PASSWORD' };
  const onFinishLogin = (loginData) => {
    axios({
      method: 'POST',
      url: '/u-consultation/auth/login',
      data: loginData,
    })
      .then((response) => {
        if (response.data.data) {
          const verifyCheck = jwtDecode(response.data.data);
          console.log(verifyCheck);
          if (verifyCheck.isPhoneVerified === true) {
            localStorage.setItem('user', verifyCheck);
            window.location.reload(true);
            history.push('/completeSignup');
          } else {
            localStorage.setItem('registrationPhoneNumber', verifyCheck.phoneNumber);
            history.push('/verify-registration');
          }
        }
        return response.data;
      })
      .catch((error) => {
        console.log(error);
        if (error.response) {
          console.log(error.response.data.message);
          const errorMessage = error.response.data.message;
          loginErrors(errorMessage, form);
        } else {
          message.error('An error occurred. Please try again.');
        }
      });
  };

  return (
    <div>
      <Row>
        <Col span='8'>
          <AuthLayout />
        </Col>
        <Col span='16'>
          <div className='umi-auth container'>
            <div className='umi-auth-form'>
              <img src='imgs/logo_icon.png' alt='umedmi' className='form-logo' />
              <h1>Sign in</h1>
              <LoginFormContext.Provider value={form}>
                <Form form={form} onFinish={onFinishLogin} initialValues={initialValues}>
                  <LoginForm />
                </Form>
              </LoginFormContext.Provider>
              <div className='auth-social'>
                <AuthSocialMedia title='Login With' />
              </div>
            </div>
          </div>

          <PolicyAndTerms authStatus=" Don't have an account?" authAction='Register.' redirectLink='/signup' />
        </Col>
      </Row>
    </div>
  );
}

export default Login;
