import React from "react";
import { Steps, Progress } from "antd";
import "../AuthProfileComplete.less";
import "./AuthMainSteps.less";
const { Step } = Steps;

function AuthMainSteps({ steps, current }) {
  return (
    <div>
      <h1>Profile Completion</h1>
      <Steps current={current} labelPlacement="vertical">
        {steps.map((item) => (
          <Step
            key={item.title}
            // title={item.title}
            description={item.description}
            icon={
              <Progress
                type="circle"
                width={60}
                percent={0}
                style={{ marginLeft: '-8px', marginTop: '-8px' }}
                format={percent =>
                  <img src={item.icon} alt='' />
                }
              />
            }
          />
        ))}
      </Steps>
      {steps.map(({ title, content }, i) => (
        <div
          key={title}
          className={i === current ? " step-fade-in" : "step-fade-out"}
        >
          <div className="steps-content">{content}</div>
        </div>
      ))}
    </div>
  );
}

export default AuthMainSteps;
