import React from 'react';
import { Collapse } from 'antd';
import ReportPanel from '../ReportPanel';
import UmiInputs from '../../Common/PanelInputs/UmiInputs';
import UmiTextarea from '../../Common/PanelInputs/UmiTextarea';
import PanelInputs from '../../Common/PanelInputs/PanelInputs';
import ExportExcel from '../../Common/ExportExcel/ExportExcel';
import PanelControl from '../../Common/PanelControl/PanelControl';
import SearchInput from '../../Common/PanelInputs/SearchInput/SearchInput';

import './Report.less';

const { Panel } = Collapse;

function Report() {
  const ExportExcelRender = () => <ExportExcel activeTab="report" tabName="Report" />;

  return (
    <Collapse defaultActiveKey={['medical-recommendations']} bordered={false}>
      <Panel header="Medical recommendations" key="medical-recommendations">
        <PanelControl moreButton panelKey="medical-recommendations">
          <ExportExcelRender />
        </PanelControl>
        <PanelInputs formTitle="formTitle" formTitleContent="Write down your medical recommendations">
          <SearchInput name="recommendations-subject" placeholder="Subject" />
          <UmiInputs type="number" name="recommendations-id" placeholder="#" />
          <UmiTextarea name="recommendations-notes" placeholder="Notes" />
          <button className="umi-btn small">Save changes</button>
        </PanelInputs>
      </Panel>
      <Panel header="Report" key="report">
        <PanelControl moreButton panelKey="report">
          <ExportExcelRender />
        </PanelControl>
        <ReportPanel />
      </Panel>
    </Collapse>
  );
}

export default Report;
