import React, { useState } from "react";
import AuthAds from "./AuthAds";
import AuthAsideSteps from "../AuthCompleteProcess/Progress/AuthAsideSteps";
import AuthAsideProgress from "../AuthCompleteProcess/Progress/AuthAsideProgress";
import "./AuthLayout.less";

type Iprops = {
  accountPercentage?: any;
  profilePercentage?: any;
};
function AuthLayout({ accountPercentage, profilePercentage }: Iprops) {
  console.log(profilePercentage, accountPercentage);

  const [adStatus, setAdStatus] = useState(false);
  const [pinStatus, setPinStatus] = useState(localStorage.getItem("pinStatus"));
  const handlePopoverClick = () => {
    setAdStatus(!adStatus);
    console.log(adStatus);
    if (pinStatus) {
      setPinStatus("false");
      localStorage.setItem("pinStatus", "");
      setAdStatus(false);
    }
  };
  const handleFixedPinClick = () => {
    if (adStatus) {
      setPinStatus("true");
      localStorage.setItem("pinStatus", "true");
    }
  };
  return (
    <div className="auth-layout">
      {localStorage.getItem("pinStatus") === "true" ? (
        <div className="new-styling-ads">
          <AuthAds
            onPopoverClick={handlePopoverClick}
            onFixedPinClick={handleFixedPinClick}
          />
        </div>
      ) : (
        <>
          {!adStatus ? (
            <>
              {profilePercentage !== undefined && (
                <>
                  <div>
                    <AuthAsideProgress profilePercentage={profilePercentage} />
                  </div>
                  <>
                    <AuthAsideSteps accountPercentage={accountPercentage} />
                  </>
                </>
              )}
              <div style={{ flexBasis: 10 }}></div>
              <div>
                <figure>
                  <img
                    src="imgs/shared.svg"
                    alt="authentication image"
                    className="img-fluid"
                  />
                </figure>
                <figcaption>
                  <p>
                    Have full access to your shared patients' documents and
                    easily collaborate with your fellow doctors.
                  </p>
                </figcaption>
              </div>

              <AuthAds
                onPopoverClick={handlePopoverClick}
                onFixedPinClick={handleFixedPinClick}
              />
            </>
          ) : (
            <div className="new-styling-ads">
              <AuthAds
                onPopoverClick={handlePopoverClick}
                onFixedPinClick={handleFixedPinClick}
              />
            </div>
          )}
        </>
      )}
    </div>
  );
}

export default AuthLayout;
