import React from "react";
import InvitationModals from "./InvitationModals/InvitationModals";
import "./Invitation.less";

function Invitation() {
  return (
    <div className="subscription-invitation">
      <div className="subscription-invitation-img">
        <img alt="subscription" src="imgs/subscription.svg" />
      </div>

      <div>
        <h1>FREE GOLD MONTH!</h1>
        <p>
          Invite 10 colleagues or more and get 1 free month of Gold subscription
        </p>
        <InvitationModals />
      </div>
    </div>
  );
}
export default Invitation;
